import { Map, List, fromJS } from 'immutable';
import * as Type from 'constants/action-types';

const INITIAL_STATE = Map({
  query: null,
  waitingForResults: null,
  results: List()
});

export default function SearchReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case Type.PERFORM_SEARCH:
      // prettier-ignore
      const { query } = action;
      return state.withMutations(mutableState =>
        mutableState.set('waitingForResults', true).set('query', query)
      );

    case Type.CLEAR_SEARCH:
      return INITIAL_STATE;

    case Type.SEARCH_RESULTS_READY:
      const { query: receivedQuery, results } = action;
      if (receivedQuery === state.get('query')) {
        return state.withMutations(mutableState =>
          mutableState
            .set('waitingForResults', false)
            .set('results', fromJS(results))
        );
      }
      return state;

    default:
      return state;
  }
}
