import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { createScroller } from 'zenscroll';

import TocPreloader from './toc-preloader';
import TocNode from './toc-node';
import { tocTreeBem, tocTreeNoFilteringResultsBem } from './_bem';

import './toc-tree.scss';

const makePath = (array, data) => {
  const item = data[array[0]];
  if (!item || !item.parentId) {
    return array;
  }
  return makePath([item.parentId, ...array], data);
};

export default class Toc extends Component {
  static propTypes = {
    filteredIds: PropTypes.array,
    isLoading: PropTypes.bool,
    data: PropTypes.object,
    selectedId: PropTypes.string,
    topLevelIds: PropTypes.array,
    className: PropTypes.string,
    onSelect: PropTypes.func,
    linkComponent: PropTypes.func,
    selectedSection: PropTypes.string
  };

  state = {
    selectedPath: [],
    selectedId: null
  };

  static getDerivedStateFromProps(props, state) {
    if (!props.selectedId || !props.data) {
      return null;
    }
    if (props.selectedId === state.selectedId) {
      return null;
    }
    return {
      selectedId: props.selectedId,
      selectedPath: makePath([props.selectedId], props.data)
    };
  }

  get topLevelIds() {
    return Object.values(this.props.data)
      .filter(page => page.level === 0 && !page.anchor)
      .map(page => page.id);
  }

  handleElementRef = ref => {
    this.scroller = createScroller(ref, null, 50);
  };

  scrollIntoView = element => {
    this.scroller && element && this.scroller.intoView(element);
  };

  handleSelect = id => {
    this.setState({
      selectedPath: makePath([id], this.props.data)
    });
    if (this.props.onSelect) {
      this.props.onSelect(id);
    }
  };

  render() {
    const {
      filteredIds,
      isLoading,
      className,
      data,
      linkComponent,
      selectedSection
    } = this.props;
    const { selectedPath } = this.state;
    const topLevelIds = this.props.topLevelIds || this.topLevelIds;
    const items = !!filteredIds
      ? topLevelIds.filter(pageId => filteredIds.includes(pageId))
      : topLevelIds;
    return !isLoading && data ? (
      <ul className={tocTreeBem.mix(className)()} ref={this.handleElementRef}>
        {items.map(pageId => (
          <TocNode
            key={pageId}
            id={pageId}
            pages={data}
            selectedPath={selectedPath.includes(pageId) && selectedPath}
            onSelect={this.handleSelect}
            scrollIntoView={this.scrollIntoView}
            filterData={filteredIds}
            linkComponent={linkComponent}
            selectedSection={selectedSection}
          />
        ))}
        {!!filteredIds && items.length === 0 && (
          <div className={tocTreeNoFilteringResultsBem()}>No results</div>
        )}
      </ul>
    ) : (
      <TocPreloader />
    );
  }
}
