import React, { Component } from 'react';
import PropTypes from 'prop-types';

import angleGlyph from 'theme/icons/angle-down.svg';

import {
  dropdownTriggerBem,
  dropdownTriggerLabelBem,
  dropdownTriggerValueBem,
  dropdownTriggerIconBem,
  dropdownTriggerGlyphBem
} from './_bem.js';

class Trigger extends Component {
  render() {
    const { onClick, children, value, label } = this.props;
    return (
      <div
        className={dropdownTriggerBem()}
        onClick={e => {
          onClick(e);
          e.stopPropagation();
        }}
      >
        {label && <span className={dropdownTriggerLabelBem()}>{label}</span>}
        {value && <span className={dropdownTriggerValueBem()}>{value}</span>}
        {children || (
          <span className={dropdownTriggerIconBem()}>
            <svg className={dropdownTriggerGlyphBem()}>
              <use xlinkHref={`#${angleGlyph.id}`} />
            </svg>
          </span>
        )}
      </div>
    );
  }
}

Trigger.propTypes = {
  label: PropTypes.string,
  value: PropTypes.string,
  children: PropTypes.node,
  onClick: PropTypes.func
};

export default Trigger;
