import { connect } from 'react-redux';
import DebouncedFilterInput from 'components/debounced-filter-input';
import { filterByTitle } from 'actions/toc-tree';

function mapStateToProps(state) {
  return {
    term: state.tree.getIn(['filterData', 'term']) || ''
  };
}

function mapDispatchToProps(dispatch) {
  return {
    filter: term => dispatch(filterByTitle(term))
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(
  DebouncedFilterInput
);
