import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { LayoutContainer } from 'components/layout';
import { Group, GroupItem } from 'components/group';
import Hamburger from 'components/hamburger';
import KeymapSwitcher from 'containers/keymap-switcher';
import SectionSwitcher from 'containers/section-switcher';
import StageWarning from 'components/stage-warning';

import searchGlyph from 'theme/icons/search.svg';
import closeGlyph from 'theme/icons/close.svg';

import {
  showNavigation,
  showSearch,
  hideNavigation,
  hideSearch
} from 'actions/app';

import {
  headerBem,
  headerContainerBem,
  headerTitleBem,
  headerLinksBem,
  headerDownloadLinkBem,
  headerSearchButtonBem,
  headerSearchIconBem,
  headerNavToggleBem
} from './_bem';
import SearchOverlay from 'containers/search-overlay';
import { clearSearch } from 'actions/search';
import { isSearchAvailable } from 'utils/is-search-available';

class Header extends Component {
  static propTypes = {
    className: PropTypes.string,
    appName: PropTypes.string,
    selectedPageId: PropTypes.string,
    isNavigationVisible: PropTypes.bool,
    isKeymapSwitcherVisible: PropTypes.bool,
    isSectionSwitcherVisible: PropTypes.bool,
    isSearchOpened: PropTypes.bool,
    isSearchEnabled: PropTypes.bool,
    productName: PropTypes.string,
    productVersion: PropTypes.string,
    productDownloadPage: PropTypes.string,
    productStage: PropTypes.string,
    productFallbackUrl: PropTypes.string,
    showStageWarningLink: PropTypes.bool,
    showDownloadButton: PropTypes.bool,
    setNavVisible: PropTypes.func,
    setSearchVisible: PropTypes.func,
    clearSearch: PropTypes.func
  };

  hamburgerHandler = () => {
    const { isNavigationVisible } = this.props;

    this.props.setNavVisible(!isNavigationVisible);
  };

  toggleSearchOverlay = () => {
    const { isSearchOpened } = this.props;
    this.props.setSearchVisible(!isSearchOpened);
    if (isSearchOpened) {
      this.props.clearSearch();
    }
  };

  closeSearchOverlay = () => {
    this.props.setSearchVisible(false);
    this.props.clearSearch();
  };

  render() {
    const {
      className,
      appName,
      productName,
      productVersion,
      productDownloadPage,
      productStage,
      productFallbackUrl,
      selectedPageId,
      showDownloadButton,
      showStageWarningLink,
      isNavigationVisible,
      isSearchEnabled,
      isKeymapSwitcherVisible,
      isSectionSwitcherVisible,
      isSearchOpened
    } = this.props;

    const isNeedToShowStageWarning =
      productStage && productStage !== 'release' && selectedPageId;

    return (
      <header className={headerBem.mix(className)()}>
        <LayoutContainer
          className={headerContainerBem({ blur: isSearchOpened })()}
        >
          <Group align="left" className={headerTitleBem()}>
            <GroupItem noMargin>
              {productName && (
                <Hamburger
                  className={headerNavToggleBem()}
                  type="close"
                  active={isNavigationVisible}
                  onClick={this.hamburgerHandler}
                  title="Show navigation"
                  white
                />
              )}
            </GroupItem>
            {productName && <GroupItem noMargin>{productName}</GroupItem>}
            &nbsp;
            {productVersion && <GroupItem noMargin>{productVersion}</GroupItem>}
            &nbsp;
            {appName && <GroupItem>{appName}</GroupItem>}
            <GroupItem>
              {isNeedToShowStageWarning && (
                <StageWarning
                  productName={productName}
                  stage={productStage}
                  fallbackUrl={productFallbackUrl}
                  selectedPageId={selectedPageId}
                  showStageWarningLink={showStageWarningLink}
                />
              )}
            </GroupItem>
          </Group>
          <Group
            align="right"
            className={headerLinksBem({ blur: isSearchOpened }).mix(
              'no-blur'
            )()}
          >
            {showDownloadButton && productDownloadPage && (
              <GroupItem>
                <a
                  className={headerDownloadLinkBem()}
                  href={productDownloadPage}
                >
                  Get {productName}
                </a>
              </GroupItem>
            )}
            {isSectionSwitcherVisible && (
              <GroupItem>
                <SectionSwitcher />
              </GroupItem>
            )}
            {isKeymapSwitcherVisible && (
              <GroupItem>
                <KeymapSwitcher />
              </GroupItem>
            )}
            {isSearchEnabled && (
              <GroupItem className="no-blur">
                <a
                  className={headerSearchButtonBem({ close: isSearchOpened })()}
                  disabled={!isSearchEnabled && !isSearchOpened}
                  onClick={this.toggleSearchOverlay}
                  title="Search"
                >
                  <svg className={headerSearchIconBem()}>
                    {isSearchOpened ? (
                      <use xlinkHref={`#${closeGlyph.id}`} />
                    ) : (
                      <use xlinkHref={`#${searchGlyph.id}`} />
                    )}
                  </svg>
                </a>
              </GroupItem>
            )}
          </Group>
        </LayoutContainer>
        {isSearchOpened && <SearchOverlay onClose={this.closeSearchOverlay} />}
      </header>
    );
  }
}

const mapStateToProps = state => {
  const { app, config, tree } = state;
  const selectedPageId = tree.get('selectedPageId');

  return {
    isNavigationVisible: app.get('isNavigationVisible'),
    isSearchOpened: app.get('isSearchOpened') || false,
    isSearchEnabled: isSearchAvailable(config),
    isKeymapSwitcherVisible:
      app.get('currentKeymap') &&
      config.getIn(['keymaps', 'all']) &&
      !config.getIn(['keymaps', 'all']).isEmpty(),
    isSectionSwitcherVisible:
      config.get('sections') && !config.get('sections').isEmpty(),
    selectedPageId,
    productName: config.get('productName'),
    productVersion: config.get('productVersion'),
    showDownloadButton: config.get('showDownloadButton'),
    productDownloadPage: config.get('downloadPage'),
    productStage: config.get('stage'),
    productFallbackUrl: config.get('fallbackUrl'),
    showStageWarningLink: !tree.getIn([
      'entities',
      'pages',
      selectedPageId,
      'doNotShowWarningLink'
    ])
  };
};

const mapDispatchToProps = dispatch => {
  return {
    setNavVisible: visible =>
      dispatch(visible ? showNavigation() : hideNavigation()),
    setSearchVisible: visible =>
      dispatch(visible ? showSearch() : hideSearch()),
    clearSearch: () => dispatch(clearSearch())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Header);
