import React, { Fragment, cloneElement } from 'react';
import shortId from 'shortid';
import renderHTML, {
  processNodes,
  convertNodeToElement
} from 'react-html-parser';

import Permalink from 'components/permalink';
import { Link } from 'components/preserve-qs-link';
import CodeBlock from 'components/codeblock';
import { LightboxLink } from 'components/lightbox';
import GifPlayer from 'components/gif-player';
import HelpTabs from 'components/tabs';
import Collapse from 'components/collapse';
import Keystroke from 'containers/keystroke';
import AnchorWaypoint from 'containers/anchor-waypoint';
import SectionBlock from 'containers/section-block';

import {
  nodeToString,
  hasClass,
  isTag,
  hasAttr,
  hasQueryStringParam
} from 'utils/parsed-html-helpers';
import SideBlock from 'containers/side-block';

let sideBlockIdsCounter = 0;

/* eslint consistent-return: off, react/no-multi-comp: off */
const codeReplacer = (domNode, index) => {
  if (domNode.type === 'tag' && domNode.name === 'code') {
    return (
      <code
        key={index}
        className={domNode.attribs.class}
        dangerouslySetInnerHTML={{
          __html: domNode.children.map(nodeToString).join('')
        }}
      />
    );
  }
};

const permalink = component => {
  const childrenWithLink = (
    <Fragment>
      {component.props.children}
      {'\u00A0'}
      <Permalink to={`#${component.props.id}`} />
    </Fragment>
  );
  return cloneElement(component, {
    children: childrenWithLink,
    key: shortId.generate()
  });
};

export const replacer = (parsedNode, index) => {
  if (hasAttr(parsedNode, 'data-bypass')) {
    return; // ignore any replace rule if there is data-bypass attribute
  } else if (
    hasClass(parsedNode, 'navigation-links') &&
    parsedNode.children.length === 0
  ) {
    return null;
  } else if (hasAttr(parsedNode, 'data-toc')) {
    const anchorId = parsedNode.attribs['data-toc'];
    return (
      <AnchorWaypoint key={anchorId} anchorId={anchorId}>
        {permalink(convertNodeToElement(parsedNode))}
      </AnchorWaypoint>
    );
  } else if (
    hasAttr(parsedNode, 'id') &&
    (isTag(parsedNode, 'h2') || isTag(parsedNode, 'h3'))
  ) {
    return permalink(convertNodeToElement(parsedNode));
  } else if (hasAttr(parsedNode, 'data-section')) {
    const sectionId = parsedNode.attribs['data-section'];
    delete parsedNode.attribs['data-section'];
    return (
      <SectionBlock key={index} correspondingSection={sectionId}>
        {processNodes([parsedNode], replacer)}
      </SectionBlock>
    );
  } else if (hasClass(parsedNode, 'code-block')) {
    const nodeChild = parsedNode.children[0];
    const isCodeHighLighted =
      nodeChild.type === 'tag' && nodeChild.children.length > 0;

    return (
      <CodeBlock
        key={index}
        isHighlighted={isCodeHighLighted}
        lang={parsedNode.attribs['data-lang']}
        code={processNodes(parsedNode.children, codeReplacer)[0]}
        clipboardId={`code-${shortId.generate()}`}
      />
    );
  } else if (hasClass(parsedNode, 'tabs')) {
    return <HelpTabs key={index} parsedDomNode={parsedNode} />;
  } else if (hasClass(parsedNode, 'collapse')) {
    const titleNode = parsedNode.children.find(node =>
      hasClass(node, 'collapse__title')
    );
    const contentNode = parsedNode.children.find(node =>
      hasClass(node, 'collapse__content')
    );

    return (
      <Collapse
        key={index}
        parsedDomNode={parsedNode}
        titleElement={processNodes(titleNode.children, replacer)}
        isExpanded={hasAttr(titleNode, 'data-expanded')}
      >
        {processNodes(contentNode.children, replacer)}
      </Collapse>
    );
  } else if (hasClass(parsedNode, 'lightbox')) {
    return (
      <LightboxLink key={index} imageSrc={parsedNode.attribs.href}>
        {processNodes(parsedNode.children)}
      </LightboxLink>
    );
  } else if (hasClass(parsedNode, 'js-gif')) {
    return (
      <GifPlayer
        key={index}
        gifSrc={parsedNode.attribs['data-gif-src']}
        coverSrc={parsedNode.attribs.src}
        width={parsedNode.attribs.width}
      />
    );
  } else if (isTag(parsedNode, 'kbd')) {
    return <Keystroke key={index} sourceNode={parsedNode} />;
  } else if (
    isTag(parsedNode, 'a') &&
    hasAttr(parsedNode, 'href') &&
    !hasQueryStringParam(parsedNode.attribs.href, 'topic')
  ) {
    return (
      <Link
        key={index}
        to={parsedNode.attribs.href}
        className={parsedNode.attribs.class}
      >
        {processNodes(parsedNode.children, replacer)}
      </Link>
    );
  } else if (hasClass(parsedNode, 'sideblock')) {
    const { rel } = parsedNode.attribs;
    const id = parsedNode.attribs.id || `sideblock-${sideBlockIdsCounter++}`;

    return <SideBlock id={id} key={id} relId={rel} content={parsedNode} />;
  }
};

export function convertArticleToReact(html) {
  return renderHTML(html, {
    transform: replacer
  });
}
