import { Map } from 'immutable';
import * as Type from 'constants/action-types';

const INITIAL_STATE = Map();

const calculateDesiredOffset = sideBlocks => {
  return sideBlocks.withMutations(mutableSideBlocks => {
    mutableSideBlocks
      .toSetSeq()
      .filter(s => typeof s.get('relOffset') !== 'number')
      .forEach(s =>
        mutableSideBlocks.setIn([s.get('id'), 'desiredOffsetTop'], null)
      );

    const orderedSideBlocksIds = mutableSideBlocks
      .toSetSeq()
      .filter(s => typeof s.get('relOffset') === 'number')
      .sortBy(s => s.get('relOffset'))
      .map(s => s.get('id'))
      .toArray();

    for (let i = 0; i < orderedSideBlocksIds.length; i++) {
      const sideBlock = mutableSideBlocks.get(orderedSideBlocksIds[i]);

      let desiredOffsetTop = sideBlock.get('relOffset');

      if (i > 0) {
        const previousSideBlock = mutableSideBlocks.get(
          orderedSideBlocksIds[i - 1]
        );
        const prevSideBlockBottom =
          previousSideBlock.get('desiredOffsetTop') +
          previousSideBlock.get('height');

        if (desiredOffsetTop < prevSideBlockBottom) {
          desiredOffsetTop = prevSideBlockBottom;
        }
      }

      mutableSideBlocks.setIn(
        [sideBlock.get('id'), 'desiredOffsetTop'],
        desiredOffsetTop
      );
    }

    return mutableSideBlocks;
  });
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case Type.STICKER_HEIGHT_CHANGED:
      const { id, height } = action;
      return calculateDesiredOffset(state.setIn([id, 'height'], height));

    case Type.SET_STICKERS_RELATIVE_BLOCKS_OFFSETS:
      return calculateDesiredOffset(
        state.map(sideBlock =>
          sideBlock.set('relOffset', action.offsets[sideBlock.get('id')])
        )
      );

    case Type.REGISTER_SIDEBLOCK: {
      /* eslint-disable-next-line */
      const { id, relId, content } = action;
      return calculateDesiredOffset(
        state.withMutations(mutableState =>
          mutableState
            .setIn([id, 'id'], id)
            .setIn([id, 'relId'], relId)
            .setIn([id, 'content'], content)
        )
      );
    }

    case Type.UNREGISTER_SIDEBLOCK: {
      return state.delete(action.id);
    }

    case Type.CLEAR_SIDEBLOCKS: {
      return INITIAL_STATE;
    }

    default:
      return state;
  }
};
