import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchConfig } from 'actions/config';
import { CONFIG_URL } from 'constants/url';
import { clearConfig } from 'actions/app';

class PathWatcher extends Component {
  static propTypes = {
    match: PropTypes.object,
    WrappedComponent: PropTypes.func,
    fetchConfig: PropTypes.func.isRequired,
    clearConfig: PropTypes.func.isRequired
  };

  componentWillMount() {
    this.props.fetchConfig();
  }

  componentWillReceiveProps(newProps) {
    const productPath = this.props.match.params[0];
    const newProductPath = newProps.match.params[0];
    if (productPath !== newProductPath) {
      this.props.fetchConfig();
    }
  }

  componentWillUnmount() {
    this.props.clearConfig();
  }

  render() {
    const { WrappedComponent, match, ...restProps } = this.props;
    return <WrappedComponent match={match} {...restProps} />;
  }
}

const mapStateToProps = () => ({});

const mapDispatchToProps = dispatch => ({
  fetchConfig: () => dispatch(fetchConfig(CONFIG_URL)),
  clearConfig: () => dispatch(clearConfig())
});

const PathWatcherContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(PathWatcher);

/* eslint react/no-multi-comp: off*/
export const wrapWithPathWatcher = wrappedComponent => ({ ...props }) => {
  return (
    <PathWatcherContainer WrappedComponent={wrappedComponent} {...props} />
  );
};
