import * as Type from 'constants/action-types';
import { Map } from 'immutable';

const initialApp = Map({
  currentKeymap: null,
  currentSection: null,
  isNavigationVisible: null,
  isSearchOpened: null,
  isMobile: null
});

export function app(state = initialApp, action) {
  switch (action.type) {
    case Type.CHANGE_KEYMAP:
      return state.set('currentKeymap', action.keymap);
    case Type.CHANGE_SECTION:
      return state.set('currentSection', action.section);

    case Type.SHOW_NAVIGATION:
      return state.set('isNavigationVisible', true);
    case Type.HIDE_NAVIGATION:
      return state.set('isNavigationVisible', false);
    case Type.SELECT_NODE:
      if (state.get('isMobile')) {
        return state.set('isNavigationVisible', false);
      }
      return state;

    case Type.SHOW_SEARCH:
      return state.set('isSearchOpened', true);
    case Type.HIDE_SEARCH:
      return state.set('isSearchOpened', false);

    case Type.CLEAR_CONFIG:
      return initialApp;

    case Type.SET_MOBILE_MODE:
      return state.set('isMobile', action.enable);

    default:
      return state;
  }
}
