import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ActionCircle from 'components/action-circle';

import { preloadImage, getFirstGifFrameSrc } from './_utils';
import { gifPlayerBem, gifPlayerImageBem, gifPlayerTriggerBem } from './_bem';

class GifPlayer extends Component {
  static propTypes = {
    gifSrc: PropTypes.string.isRequired,
    coverSrc: PropTypes.string,
    width: PropTypes.string,
    isPlaying: PropTypes.bool
  };

  constructor(props) {
    super(props);

    this.state = {
      isPlaying: props.isPlaying || false
    };
  }

  componentWillMount() {
    this.updateImages(this.props);
  }

  componentWillReceiveProps(nextProps) {
    this.updateImages(nextProps, this.props);
  }

  updateImages(newProps, oldProps = {}) {
    const { gifSrc: nextGifSrc, coverSrc: nextCoverSrc } = newProps;
    const { gifSrc: prevGifSrc, coverSrc: prevCoverSrc } = oldProps;

    const imagesHaveNotChanged =
      nextGifSrc === prevGifSrc && nextCoverSrc === prevCoverSrc;
    const isNeedToCreateCover = !nextCoverSrc && nextGifSrc !== prevGifSrc;

    if (imagesHaveNotChanged) {
      return;
    }

    if (isNeedToCreateCover) {
      preloadImage(nextGifSrc).then(({ image }) => {
        this.setState({
          coverSrc: getFirstGifFrameSrc(image) || nextGifSrc
        });
      });
    }

    this.setState({
      gifSrc: nextGifSrc,
      coverSrc: nextCoverSrc
    });
  }

  loadAndPlayGif() {
    this.setState({ isLoading: true });
    return fetch(this.props.gifSrc)
      .then(response => {
        return response.blob();
      })
      .then(blob => {
        this.setState({
          isLoading: false,
          isPlaying: true,
          gifObjectUrl: URL.createObjectURL(blob)
        });
      });
  }

  toggle = () => {
    if (!this.state.gifObjectUrl && !this.state.isPlaying) {
      this.loadAndPlayGif();
    } else {
      this.setState(({ isPlaying }) => ({
        isPlaying: !isPlaying
      }));
    }
  };

  render() {
    const { coverSrc, isPlaying, isLoading, gifObjectUrl } = this.state;
    const { width } = this.props;

    let playerSrc = '';
    if (!isPlaying) {
      playerSrc = coverSrc;
    } else if (isPlaying && gifObjectUrl) {
      playerSrc = gifObjectUrl;
    }

    return (
      <div className={gifPlayerBem()} onClick={this.toggle}>
        {!isPlaying && (
          <ActionCircle
            className={gifPlayerTriggerBem()}
            isLoading={isLoading}
            dashed
            text="GIF"
          />
        )}
        <img className={gifPlayerImageBem()} src={playerSrc} width={width} />
      </div>
    );
  }
}

export default GifPlayer;
