import React from 'react';
import PropTypes from 'prop-types';

import { hamburgerBevis, hamburgerIconBevis } from './_bem';

const Hamburger = ({ className, type, active, white, title, onClick }) => (
  <div
    className={hamburgerBevis({ type, active, white }).mix(className)()}
    onClick={onClick}
    title={title}
  >
    <div className={hamburgerIconBevis()} />
  </div>
);

Hamburger.propTypes = {
  className: PropTypes.string,
  type: PropTypes.string.isRequired,
  active: PropTypes.bool,
  white: PropTypes.bool,
  title: PropTypes.string,
  onClick: PropTypes.func.isRequired
};

export default Hamburger;
