import Url from 'url';
import path from 'path';

export const getAbsoluteUrl = (() => {
  const a = document.createElement('a');
  return url => {
    a.href = url;
    return a.href;
  };
})();

export const getRelativeUrl = url => {
  const { pathname, query, hash } = Url.parse(url);
  return path.resolve(
    location.pathname,
    `${pathname}${query || ''}${hash || ''}`
  );
};
