import React from 'react';
import PropTypes from 'prop-types';
import { TabList, TabPanel, Tabs, Tab } from 'react-tabs';
import { replacer } from 'utils/article-to-jsx';
import { convertNodeToElement } from 'react-html-parser';
import { extractTabsData } from './utils';

const HelpTabs = ({ parsedDomNode }) => {
  const { tabsTitles, tabsContent } = extractTabsData(parsedDomNode);
  return (
    <Tabs id={parsedDomNode.attribs.id}>
      <TabList>
        {tabsTitles.map((title, index) => <Tab key={index}>{title}</Tab>)}
      </TabList>
      {tabsContent.map((content, index) => (
        <TabPanel key={index}>
          {convertNodeToElement(content, index, replacer)}
        </TabPanel>
      ))}
    </Tabs>
  );
};

HelpTabs.propTypes = {
  parsedDomNode: PropTypes.object.isRequired
};

export default HelpTabs;
