import { Component } from 'react';
import { convertNodeToElement } from 'react-html-parser';
import PropTypes from 'prop-types';

import { replacer } from 'utils/article-to-jsx';

class SideBlock extends Component {
  static propTypes = {
    isMobile: PropTypes.bool,
    positionedOnSidePanel: PropTypes.bool,
    registerSideBlock: PropTypes.func.isRequired,
    unregisterSideBlock: PropTypes.func.isRequired,
    id: PropTypes.string,
    content: PropTypes.object,
    relId: PropTypes.string
  };

  constructor(props) {
    super(props);

    const { id, content } = props;

    props.registerSideBlock();
    this.content = convertNodeToElement(content, id, replacer);
  }

  componentWillUnmount() {
    this.props.unregisterSideBlock();
  }

  render() {
    const { isMobile, positionedOnSidePanel } = this.props;

    return isMobile || !positionedOnSidePanel ? this.content : null;
  }
}

export default SideBlock;
