import React, { Component } from 'react';
import PropTypes from 'prop-types';
import bemCn from 'utils/bem-class-name/bem-class-name';
import closeGlyph from 'theme/icons/close.svg';

const cn = bemCn('feedback-popup');

const EMAIL_REGEXP = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

export default class FeedbackPopup extends Component {
  static propTypes = {
    className: PropTypes.string,
    closeHandler: PropTypes.func.isRequired,
    feedbackSupportLink: PropTypes.string,
    isEmailRequired: PropTypes.bool,
    privacyPolicyMessage: PropTypes.string.isRequired,
    sendFeedback: PropTypes.func.isRequired
  };

  static defaultProps = {
    isEmailRequired: false,
    privacyPolicyMessage:
      'By submitting this form I agree to <a href="https://www.jetbrains.com/company/privacy.html">JetBrains Privacy Policy</a>'
  };

  state = {
    note: {
      value: '',
      isTouched: false
    },
    name: {
      value: '',
      isTouched: false
    },
    email: {
      value: '',
      isTouched: false
    }
  };

  checkIsEmailValid = ({ value }) => {
    const { isEmailRequired } = this.props;
    const isValid = EMAIL_REGEXP.test(value);

    return isEmailRequired ? isValid : value === '' || isValid;
  };

  checkIsFormValid = () => {
    const { note, email } = this.state;
    return !!note.value && this.checkIsEmailValid(email);
  };

  handleInputChange = inputName => event =>
    this.setState({
      [inputName]: {
        value: event.target.value,
        isTouched: true
      }
    });

  handleFormSubmit = event => {
    event.preventDefault();

    const isFormValid = this.checkIsFormValid();

    if (!isFormValid) {
      return;
    }

    const { sendFeedback, closeHandler } = this.props;
    const { note, name, email } = event.target;

    sendFeedback({
      content: note.value,
      name: name.value,
      email: email.value
    });

    closeHandler({ chosenOption: 'no' });
  };

  render() {
    const {
      className,
      closeHandler,
      privacyPolicyMessage,
      feedbackSupportLink,
      isEmailRequired
    } = this.props;
    const { note, name, email } = this.state;

    const isFormValid = this.checkIsFormValid();

    return (
      <form
        className={cn.mix(className)()}
        onSubmit={this.handleFormSubmit}
        autoComplete="on"
      >
        <legend className={cn('legend')()}>How can we improve the page?</legend>
        <div className={cn('field')()}>
          <div className={cn('description')()}>
            Enter your Name and Email if you would like to receive
            a&nbsp;follow&nbsp;up, or{' '}
            <a target="_blank" href={feedbackSupportLink}>
              contact our support
            </a>
            .
          </div>
        </div>
        <div className={cn('field')()}>
          <textarea
            className={cn('textarea', {
              invalid: note.isTouched && note.value === ''
            })()}
            onChange={this.handleInputChange('note')}
            value={note.value}
            name="note"
            placeholder="Note*"
            autoFocus
          />
        </div>
        <div className={cn('field')()}>
          <input
            className={cn('input')()}
            onChange={this.handleInputChange('name')}
            value={name.value}
            type="text"
            autoComplete="name"
            name="name"
            placeholder="Name"
          />
        </div>
        <div className={cn('field')()}>
          <input
            className={cn('input', {
              invalid: email.isTouched && !this.checkIsEmailValid(email)
            })()}
            onChange={this.handleInputChange('email')}
            value={email.value}
            type="email"
            name="email"
            placeholder={`Email${isEmailRequired ? '*' : ''}`}
          />
        </div>
        {privacyPolicyMessage && (
          <div className={cn('field')()}>
            <div
              className={cn('privacy-policy')()}
              dangerouslySetInnerHTML={{ __html: privacyPolicyMessage }}
            />
          </div>
        )}
        <button
          className={cn('button', { disabled: !isFormValid })()}
          type="submit"
          name="Send"
        >
          Send
        </button>
        <a
          className={cn('close-button')()}
          onClick={closeHandler}
          aria-label="Close"
          title="Close"
        >
          <svg className={cn('close-icon')()}>
            <use xlinkHref={`#${closeGlyph.id}`} />
          </svg>
        </a>
      </form>
    );
  }
}
