import React from 'react';
import PropTypes from 'prop-types';

import { lightboxCloseButtonBem, lightboxOverlayBem } from './_bem';

const LightBoxOverlay = ({ onClose }) => (
  <div className={lightboxOverlayBem()} onClick={onClose}>
    <div className={lightboxCloseButtonBem()} onClick={onClose} title="Close" />
  </div>
);

LightBoxOverlay.propTypes = {
  onClose: PropTypes.func
};

export default LightBoxOverlay;
