import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { lightboxContentBem, lightboxLoadingIconBem } from './_bem';
import ActionCircle from '../action-circle/action-circle';

export default class LightboxContent extends Component {
  static propTypes = {
    imageSrc: PropTypes.string.isRequired,
    onClose: PropTypes.func
  };

  constructor(props) {
    super(props);

    this.state = {
      imageSrc: null
    };

    this.loadImage(props.imageSrc).then(imageSrc => {
      this.setState({
        imageSrc
      });
    });
  }

  loadImage(url) {
    return fetch(url)
      .then(response => {
        return response.blob();
      })
      .then(blob => {
        return URL.createObjectURL(blob);
      });
  }

  render() {
    const { imageSrc } = this.state;

    return (
      <div
        className={lightboxContentBem({ loading: !imageSrc })()}
        onClick={e => e.stopPropagation()}
      >
        {imageSrc ? (
          <img src={imageSrc} />
        ) : (
          <ActionCircle
            isLoading
            text="..."
            className={lightboxLoadingIconBem()}
          />
        )}
      </div>
    );
  }
}
