import algoliasearch from 'algoliasearch';
import QueryString from 'query-string';

import { APPLICATION_ID, API_KEY, INDEX_NAME } from 'constants/algolia';
import * as Type from 'constants/action-types';

import { getAbsoluteUrl, getRelativeUrl } from 'utils/url-helpers';
import { handleError } from './error';

const algoliaClient = algoliasearch(APPLICATION_ID, API_KEY);
const algoliaIndex = algoliaClient.initIndex(INDEX_NAME);

export const searchResultsReady = (query, results) => ({
  type: Type.SEARCH_RESULTS_READY,
  query,
  results
});

export const performSearch = query => {
  return (dispatch, getState) => {
    const searchServiceUrl = getState().config.get('searchService');
    const stage = getState().config.get('stage');

    const productId = getState().config.get('productId');
    const productVersion = getState().config.get('productVersion');

    if (productId) {
      dispatch({ type: Type.PERFORM_SEARCH, query });

      return algoliaIndex
        .search({
          facetFilters: [`product:${productId}`, `version:${productVersion}`],
          query: query,
          attributesToRetrieve: ['pageTitle', 'url'],
          attributesToSnippet: ['content:80'],
          snippetEllipsisText: ''
        })
        .then(({ hits }) => {
          return hits.map(({ pageTitle, url, _snippetResult }) => ({
            title: pageTitle,
            snippet: _snippetResult.content.value,
            relativeUrl: getRelativeUrl(url),
            absoluteUrl: getAbsoluteUrl(url)
          }));
        })
        .then(data => dispatch(searchResultsReady(query, data)))
        .catch(error => {
          dispatch(searchResultsReady(query, []));
          error.message = error.message + ` Query: ${query}`;

          return dispatch(handleError(error, 'search'));
        });
    } else if (searchServiceUrl) {
      // TODO: remove after productId appears in all configs
      dispatch({ type: Type.PERFORM_SEARCH, query });

      const queryString = QueryString.stringify({
        q: query,
        origin: window.location.pathname,
        stage
      });

      return fetch(`${searchServiceUrl}?${queryString}`)
        .then(data => data.json())
        .then(({ results }) => {
          return results.map(({ title, url, snippet }) => ({
            title,
            snippet,
            relativeUrl: url,
            absoluteUrl: getAbsoluteUrl(url)
          }));
        })
        .then(data => dispatch(searchResultsReady(query, data)))
        .catch(error => {
          dispatch(searchResultsReady(query, []));
          error.message = error.message + ` Query: ${query}`;

          return dispatch(handleError(error, 'search'));
        });
    }

    return Promise.reject(
      new Error('Search service URL is missing from config')
    );
  };
};

export const clearSearch = () => ({
  type: Type.CLEAR_SEARCH
});
