import MQ from 'mediaquery';

export const SMALL = 'small';
export const MEDIUM = 'medium';
export const LARGE = 'large';
export const XLARGE = 'xlarge';

export const BreakPoints = {
  [SMALL]: 640, // phone
  [MEDIUM]: 960, // tablet
  [LARGE]: 1200, // tablet, mini-laptop
  [XLARGE]: 1440 // laptop, desktop
};

// Object for `media-provider` component
const MediaQueries = MQ.asObject(BreakPoints);
export const MatchQueries = Object.keys(MediaQueries).map(key => ({
  name: key,
  mql: window.matchMedia ? window.matchMedia(MediaQueries[key]) : null
}));

export default MediaQueries;
