import React, { Component } from 'react';
import { sidePanelBem } from './_bem';
import PropTypes from 'prop-types';

import Sticker from 'containers/sticker';

import { getCumulativeOffsetTop } from 'utils/get-cumulative-offest';
import debounced from 'utils/debounce';

class SidePanel extends Component {
  static propTypes = {
    sideBlocks: PropTypes.object,
    className: PropTypes.string,
    setStickersRelativeBlocksOffsets: PropTypes.func
  };

  constructor(props) {
    super(props);

    this.debouncedSetStickersRelativeBlocksOffsets = debounced(
      props.setStickersRelativeBlocksOffsets,
      200
    );
  }

  componentDidMount() {
    this.watchElement = document.querySelector('.article');
    window.requestAnimationFrame(this.checkAndRefreshOffsets);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.sideBlocks.size !== nextProps.sideBlocks.size) {
      this.refreshOffsets(nextProps);
    }
  }

  refreshOffsets = (props = this.props) => {
    const { sideBlocks } = props;
    const offsets = {};

    sideBlocks.forEach(sideBlock => {
      const relativeElement = document.getElementById(sideBlock.get('relId'));
      if (relativeElement) {
        offsets[sideBlock.get('id')] = getCumulativeOffsetTop(
          relativeElement,
          '.article'
        );
      }
    });

    this.debouncedSetStickersRelativeBlocksOffsets(offsets);
  };

  checkAndRefreshOffsets = () => {
    if (this.prevScrollHeight !== this.watchElement.scrollHeight) {
      this.refreshOffsets();
      this.prevScrollHeight = this.watchElement.scrollHeight;
    }

    window.requestAnimationFrame(this.checkAndRefreshOffsets);
  };

  render() {
    const { sideBlocks, className } = this.props;
    const sideBlocksOrderedIds = sideBlocks
      .toSetSeq()
      .sortBy(s => s.get('relOffset'))
      .map(s => s.get('id'))
      .toArray();

    return (
      <div className={`${className} ${sidePanelBem()}`}>
        {sideBlocksOrderedIds.map(id => (
          <Sticker key={id} id={id} />
        ))}
      </div>
    );
  }
}

export default SidePanel;
