import { List } from 'immutable';
import * as Type from 'constants/action-types';
import { selectNode } from 'actions/toc-tree';

export const scrollOnSelectArticleMiddleware = store => next => {
  return action => {
    const state = store.getState();

    // executing before action reach reducers,
    // substitute SELECT_PREV_ANCHOR_NODE with proper SELECT_NODE action using data from store
    /* eslint consistent-return: off*/
    if (
      (action.type === Type.SELECT_ANCHOR_NODE ||
        action.type === Type.SELECT_PREV_ANCHOR_NODE) &&
      state.article.get('isScrolling')
    ) {
      return next(action);
    }
    if (action.type === Type.SELECT_PREV_ANCHOR_NODE) {
      const { anchorNodeId } = action;
      const selectedPageId = state.tree.get('selectedPageId');
      const allSections = state.config.get('sections') || List();
      const currentSection =
        state.app.get('currentSection') || allSections.get(0);

      const selectedPageEnabledAnchors = state.tree
        .getIn(['entities', 'pages', selectedPageId, 'anchors'], [])
        .filter(anchorId => {
          const anchor = state.tree.getIn(['entities', 'anchors', anchorId]);
          const section = anchor.get('section');
          return !section || section === currentSection;
        });
      const anchorIndex =
        selectedPageEnabledAnchors &&
        selectedPageEnabledAnchors.indexOf(anchorNodeId);
      if (anchorIndex > 0) {
        return next(
          selectNode(selectedPageEnabledAnchors.get(anchorIndex - 1), true)
        );
      } else if (anchorIndex === 0) {
        return next(selectNode(selectedPageId, true));
      } else if (anchorNodeId === selectedPageId) {
        // we are above the header select current page in toc
        return next(selectNode(selectedPageId, true));
      }
      return; // there is no such anchor in current article, discard this action
    } else if (action.type === Type.SELECT_ANCHOR_NODE) {
      return next(selectNode(action.anchorNodeId));
    }

    return next(action);
  };
};
