import { fromJS, List, Map, Set } from 'immutable';

import * as ActionTypes from 'constants/action-types';
import escapeRegExp from './utils/escape-regexp';
import normalizeTreeData from './utils/normalize-tree-data';
import { getPathToRoot, isExpandable } from './utils/expanding-helpers';
import { filterTree } from './utils/filtering-helpers';

export const INITIAL_STATE = Map({
  entities: Map({
    pages: Map(),
    anchors: Map()
  }),
  topLevelIds: List(),
  selectedPageId: null,
  selectedAnchorId: null,
  pendingSelectedId: null, // variable to store selected Id before TOC content is loaded
  expandedIds: Set(),
  treeDataReady: false,
  filterData: Map()
});

export default function TreeReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case ActionTypes.TREE_DATA_READY: {
      const { treeData } = action;
      const normalizedData = treeData.entities
        ? fromJS(treeData)
        : fromJS(normalizeTreeData(treeData));
      const pages = normalizedData.getIn(['entities', 'pages']);
      const anchors = normalizedData.getIn(['entities', 'anchors']);
      const pendingSelectedId = state.get('pendingSelectedId');
      let expandedIds = state.get('expandedIds') || Set();
      let pageId = state.get('selectedPageId');
      let anchorId = state.get('selectedAnchorId');

      expandedIds = expandedIds.merge(
        normalizedData.get('expandedIds') || Set()
      );

      if (pendingSelectedId) {
        if (pages.has(pendingSelectedId)) {
          pageId = pendingSelectedId;
        } else if (anchors && anchors.has(pendingSelectedId)) {
          pageId = pendingSelectedId.split('#')[0];
          anchorId = pendingSelectedId;
        } else if (pages.has(pendingSelectedId.split('#')[0])) {
          pageId = pendingSelectedId.split('#')[0];
        }

        const expandable = isExpandable(pages.get(pageId));
        expandedIds = Set(getPathToRoot(pages, pageId, expandable));
      }

      return state.withMutations(mutableState => {
        return mutableState
          .merge(normalizedData)
          .set('selectedPageId', pageId)
          .set('selectedAnchorId', anchorId)
          .set('pendingSelectedId', null)
          .set('expandedIds', expandedIds)
          .set('treeDataReady', true);
      });
    }

    case ActionTypes.EXPAND_NODE: {
      const { nodeId } = action;
      const pages = state.getIn(['entities', 'pages']);
      const page = pages.get(nodeId);
      const expandable = isExpandable(page);
      const pathToRoot = getPathToRoot(pages, nodeId, expandable);
      return state.update('expandedIds', expandedIds =>
        expandedIds.union(pathToRoot)
      );
    }

    case ActionTypes.COLLAPSE_NODE: {
      const { nodeId } = action;
      return state.update('expandedIds', expandedIds =>
        expandedIds.filter(id => id !== nodeId)
      );
    }

    case ActionTypes.SELECT_NODE: {
      const { nodeId } = action;
      const treeDataReady = state.get('treeDataReady');
      const pages = state.getIn(['entities', 'pages']);
      const anchors = state.getIn(['entities', 'anchors']);
      let pageId = null;
      let anchorId = null;
      if (!treeDataReady) {
        return state.set('pendingSelectedId', nodeId);
      } else if (pages.has(nodeId)) {
        pageId = nodeId;
        anchorId = null;
      } else if (anchors && anchors.has(nodeId)) {
        pageId = nodeId.split('#')[0];
        anchorId = nodeId;
      } else {
        const maybePageId = nodeId.split('#')[0];
        if (pages.has(maybePageId)) {
          pageId = maybePageId;
        }
      }

      const page = pages.get(pageId);
      if (page && page.get('selectFirstChildOnClick')) {
        const firstChild = page.getIn(['pages', 0]);
        if (firstChild) {
          pageId = firstChild;
        }
      }

      const pathToRoot = getPathToRoot(pages, pageId, true);
      return state.withMutations(muttableState => {
        return muttableState
          .set('selectedPageId', pageId)
          .set('selectedAnchorId', anchorId)
          .update('expandedIds', expandedIds => expandedIds.union(pathToRoot));
      });
    }

    case ActionTypes.FILTER_BY_TITLE: {
      const { term } = action;
      const expandedIds = state.get('expandedIds');
      const topLevelIds = state.get('topLevelIds');
      const selectedId = state.get('selectedPageId');
      const pages = state.getIn(['entities', 'pages']);

      let showingIds = Set();
      let newExpandedIds = expandedIds;
      let filteringTopLevelIds = null;
      if (term) {
        const filterRegexp = new RegExp(
          `(?:^|\\s)${escapeRegExp(action.term)}`,
          'i'
        );

        // get ids of all pages which title matches regexp and their parents
        showingIds = Set(filterTree(pages, filterRegexp));
        // extract visible topLevelIds
        filteringTopLevelIds = showingIds.filter(id =>
          topLevelIds.includes(id)
        );
        // expand all
        newExpandedIds = showingIds.filter(id => isExpandable(pages.get(id)));
      } else {
        showingIds = null;
        // if something selected during filtering expand tree till this node
        newExpandedIds = getPathToRoot(pages, selectedId);
      }
      return state.withMutations(mutableState => {
        return mutableState
          .setIn(['filterData', 'showingIds'], showingIds)
          .setIn(['filterData', 'term'], term)
          .setIn(['filterData', 'topLevelIds'], filteringTopLevelIds)
          .set('expandedIds', newExpandedIds);
      });
    }
    default: {
      return state;
    }
  }
}
