import { connect } from 'react-redux';
import Feedback from 'components/feedback';
import { sendFeedback } from 'actions/article';

const mapStateToProps = state => ({
  selectedPageId: state.tree.get('selectedPageId'),
  isEmailRequired: state.config.get('feedbackRequireEmail'),
  feedbackSupportLink: state.config.get('feedbackSupport')
});

const mapDispatchToProps = dispatch => ({
  sendFeedback: feedback => dispatch(sendFeedback(feedback))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Feedback);
