const MESSAGE_FOR_THE_HOME_PAGE =
  'Visit the home page of the current documentation';
const MESSAGE_FOR_THE_SAME_PAGE = 'View this page in the current documentation';

export const parsePath = (pathname, mask) => {
  const result = {};

  // path array begins from rootPath (slicing pathname by index of rootPath and creating an array splitting the result by '/')
  const path = pathname
    .slice(pathname.indexOf('/help'))
    .split('/')
    .slice(1);

  mask.split(':').forEach((item, index) => {
    result[item] = path[index] ? '/' + path[index] : '';
  });

  return result;
};

// FIXME: Check the logic and improve this function
export const createArticleLinkByStage = (stage, articleId, fallbackUrl) => {
  let url;
  let text;

  const topic = articleId ? '?topic=' + encodeURIComponent(articleId) : '';

  if (stage === 'eap') {
    if (fallbackUrl.indexOf('www.jetbrains.com') === -1) {
      text = MESSAGE_FOR_THE_HOME_PAGE;
      url = fallbackUrl;
    } else {
      url = fallbackUrl ? fallbackUrl + topic : '';
    }
  } else if (stage === 'outdated') {
    if (fallbackUrl && fallbackUrl.indexOf('www.jetbrains.com') !== -1) {
      url = fallbackUrl + topic;
    } else {
      const path = parsePath(location.pathname, 'root:product:version:page');
      const isTeamTool =
        path.product === 'youtrack' || path.version === 'vs-addin';

      url = path.product
        ? path.root +
          path.product +
          (isTeamTool && path.version ? path.version : '') +
          '/' +
          topic
        : '';
    }
  }

  text = text || MESSAGE_FOR_THE_SAME_PAGE;

  return url ? { url, text } : null;
};
