import React from 'react';
import { Router, Route, Switch } from 'react-router-dom';

import Header from 'containers/header';
import WebHelp from 'containers/web-help';
import Home from 'components/home-page';
import YouTrackHome from 'components/youtrack-home-page';
import DevTools from 'containers/dev-tools';

import { wrapWithPathWatcher } from 'containers/path-watcher';

/* eslint react/prop-types: off */
/* prettier-ignore */
export default ({ history }) =>
  <Router history={history}>
    <div className="layout">
      <Header appName="Help" />
      <Switch>
        <Route path="/help(/|/index.html)" component={Home} />
        <Route path="/help/youtrack(/|/index.html)" component={YouTrackHome} />
        <Route path="/**/:articlePath" component={wrapWithPathWatcher(WebHelp)}/>
      </Switch>
      {/* DevTools are only included in Development Enviroment. There is no conditional rendering
          used here, since otherwise WebPack would include DevTools into the bundle even for
          Production mode, not only Development one. See devtools/index.js for the corresponding
          code. */}
      <DevTools />
    </div>
  </Router>;
