import { connect } from 'react-redux';
import Nav from 'components/nav';
import { isSearchAvailable } from '../utils/is-search-available';

function mapStateToProps({ config }) {
  const searchService = config.get('searchService');
  return {
    isFilterEnabled: searchService !== null && !isSearchAvailable(config)
  };
}

export default connect(mapStateToProps)(Nav);
