import { createStore, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';
import rootReducer from 'reducers';

import { tocMiddleware } from 'middlewares/toc';
import { handleErrorsMiddleware } from 'middlewares/handle-errors';
import { reportErrorsMiddleware } from 'middlewares/report-errors';
import { syncSearchWithUrl } from 'middlewares/search-url';

import DevTools from 'containers/dev-tools';
import { scrollOnSelectArticleMiddleware } from 'middlewares/scroll-on-select-node';

const BUNDLE_DEV_TOOLS = Boolean(process.env.BUNDLE_DEV_TOOLS);
const IS_DEVELOPMENT = process.env.NODE_ENV === 'development';
const reduxDevtoolsExtensionCompose =
  window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__;

const appliedMiddlewares = applyMiddleware(
  scrollOnSelectArticleMiddleware,
  thunk,
  tocMiddleware,
  handleErrorsMiddleware,
  reportErrorsMiddleware,
  syncSearchWithUrl
);

let middlewares = appliedMiddlewares;

if (BUNDLE_DEV_TOOLS) {
  middlewares = compose(
    middlewares,
    DevTools.instrument()
  );
} else if (IS_DEVELOPMENT && reduxDevtoolsExtensionCompose) {
  middlewares = reduxDevtoolsExtensionCompose(middlewares);
}

const configureStore = initialState => {
  const store = createStore(rootReducer, initialState, middlewares);

  if (IS_DEVELOPMENT) {
    window.store = store;
  }

  // Enable Webpack hot module replacement for reducers
  if (module.hot && IS_DEVELOPMENT) {
    module.hot.accept('reducers', () => {
      const nextRootReducer = require('reducers').default;
      store.replaceReducer(nextRootReducer);
    });
  }

  return store;
};

export default configureStore;
