import React, { PureComponent, createRef } from 'react';
import PropTypes from 'prop-types';
import Prism from 'prismjs';
import Clipboard from 'clipboard';
import 'prismjs/plugins/normalize-whitespace/prism-normalize-whitespace';

import emptySelection from 'utils/empty-selection';
import cn from 'utils/bem-class-name';

import getGrammar from './languages';

import 'prismjs/themes/prism.css';

const bemCn = cn('code-block');

export default class CodeBlock extends PureComponent {
  static propTypes = {
    code: PropTypes.oneOfType([PropTypes.element, PropTypes.string]).isRequired,
    isHighlighted: PropTypes.bool.isRequired,
    lang: PropTypes.string.isRequired,
    clipboardId: PropTypes.string.isRequired
  };

  state = {
    showSuccessState: false
  };

  componentDidMount() {
    const clipboard = new Clipboard(this.clipboardTrigger.current, {
      target: this.clipboardTarget.current
    });
    clipboard.on('success', this.handleCopySuccess);
  }

  getHighlightedCode = code => {
    const highLightedCode = Prism.highlight(code, getGrammar(this.props.lang));
    const normalizedHighLightedCode = Prism.plugins.NormalizeWhitespace.normalize(
      highLightedCode,
      {
        'remove-trailing': true,
        'remove-indent': true,
        'left-trim': true,
        'right-trim': true
      }
    );
    return (
      <code
        className="code-block__wrapper"
        dangerouslySetInnerHTML={{
          __html: normalizedHighLightedCode
        }}
      />
    );
  };

  handleCopySuccess = () => {
    this.setState({ showSuccessState: true }, () => {
      setTimeout(() => this.setState({ showSuccessState: false }), 2000);
    });

    emptySelection();
  };

  clipboardTrigger = createRef();

  clipboardTarget = createRef();

  render() {
    const { showSuccessState } = this.state;
    const { isHighlighted, code, clipboardId } = this.props;

    return (
      <div className={bemCn()}>
        <a
          ref={this.clipboardTrigger}
          data-clipboard-target={`#${clipboardId}`}
          className={bemCn('copy-button')({ success: showSuccessState })()}
        >
          <span className={bemCn('copy-button-icon')()} />
        </a>
        <pre ref={this.clipboardTarget} id={clipboardId}>
          {isHighlighted ? code : this.getHighlightedCode(code)}
        </pre>
      </div>
    );
  }
}
