import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { UnmountClosed as ReactCollapse } from 'react-collapse';

import {
  collapseBem,
  collapseButtonBem,
  collapseButtonLineBem,
  collapseTitleBem,
  collapseContentBem
} from './_bem';

export default class Collapse extends Component {
  static propTypes = {
    titleElement: PropTypes.node.isRequired,
    children: PropTypes.oneOfType([
      PropTypes.arrayOf(PropTypes.node),
      PropTypes.node
    ]).isRequired,
    isExpanded: PropTypes.bool
  };

  constructor(props) {
    super(props);

    const { isExpanded = false } = props;

    this.state = {
      isExpanded
    };
  }

  handleTitleClick = () => {
    this.setState({
      isExpanded: !this.state.isExpanded
    });
  };

  render() {
    const { isExpanded } = this.state;
    const { titleElement, children } = this.props;

    return (
      <div className={collapseBem()}>
        <div className={collapseTitleBem()} onClick={this.handleTitleClick}>
          <svg className={collapseButtonBem({ expanded: isExpanded })()}>
            <circle cx="9.5" cy="9.5" r="9" />
            <path className={collapseButtonLineBem()} d="M9.5,5.5 L9.5,13.5" />
            <path
              className={collapseButtonLineBem({ horizontal: true })()}
              d="M5.5,9.5 L13.5,9.5"
            />
          </svg>
          {titleElement}
        </div>
        <ReactCollapse
          theme={{ content: collapseContentBem() }}
          isOpened={isExpanded}
        >
          {children}
        </ReactCollapse>
      </div>
    );
  }
}
