import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import zenscroll from 'zenscroll';
import Helmet from 'react-helmet';

import Article from 'containers/article';
import Nav from 'containers/nav';
import Footer from 'components/footer';
import DisqusThread from 'containers/disqus-thread';
import Feedback from 'containers/feedback';

import {
  appBem,
  appNavBem,
  appMainBem,
  appContainerBem,
  appContentBem,
  appFooterBem,
  appDisqusBem,
  appFeedbackBem
} from './_bem';
import { SCROLL_TOP_EDGE_OFFSET } from 'constants/scroll';

import MediaQueries, { SMALL } from 'constants/media-queries';
import { setMobileMode } from 'actions/app';
const mediaQuerySmall =
  window.matchMedia && window.matchMedia(MediaQueries[SMALL]);

class WebHelp extends Component {
  static propTypes = {
    productName: PropTypes.string,
    isNavigationVisible: PropTypes.bool,
    isSearchOpened: PropTypes.bool.isRequired,
    isDisqusEnabled: PropTypes.bool,
    articleIsReady: PropTypes.bool,
    articleUsesSideBlocks: PropTypes.bool,
    location: PropTypes.instanceOf(Object).isRequired,
    selectedArticleTitle: PropTypes.string,
    selectedPageId: PropTypes.string,
    feedbackEmail: PropTypes.string,
    isTocDisabled: PropTypes.bool,
    setMobileMode: PropTypes.func
  };

  componentDidMount() {
    const edgeOffset = -SCROLL_TOP_EDGE_OFFSET; // offset from top of container to element that will scroll into view

    this.scroller = zenscroll.createScroller(
      this.mainContainerRef,
      null,
      edgeOffset
    );

    if (mediaQuerySmall) {
      mediaQuerySmall.addListener(this.screenWidthChange);
      this.props.setMobileMode(mediaQuerySmall.matches);
    }
  }

  componentWillUnmount() {
    if (mediaQuerySmall) {
      mediaQuerySmall.removeListener(this.screenWidthChange);
    }
  }

  screenWidthChange = ({ matches }) => {
    this.props.setMobileMode(matches);
  };

  scrollToElement = (element, ...args) => {
    this.scroller && element && this.scroller.to(element, ...args);
  };

  scrollToY = (y = 0, duration = 0) => {
    this.scroller && this.scroller.toY(y, duration);
  };

  render() {
    const {
      productName,
      isNavigationVisible,
      isSearchOpened,
      isDisqusEnabled,
      articleIsReady,
      articleUsesSideBlocks,
      selectedArticleTitle,
      feedbackEmail,
      isTocDisabled
    } = this.props;

    return (
      <div className={appBem({ blur: isSearchOpened })()}>
        <Helmet
          title={
            selectedArticleTitle
              ? `${selectedArticleTitle} - Help | ${productName}`
              : `${productName || ''} Help`
          }
        />
        {!isTocDisabled && (
          <div className={appNavBem({ visible: isNavigationVisible })()}>
            <Nav />
          </div>
        )}
        <div
          className={appMainBem({ 'no-toc': isTocDisabled })()}
          ref={ref => (this.mainContainerRef = ref)}
        >
          <div className={appContainerBem()}>
            <main className={appContentBem()}>
              <Article
                scrollToElement={this.scrollToElement}
                scrollToY={this.scrollToY}
              />
              {articleIsReady &&
                (isDisqusEnabled ? (
                  <DisqusThread
                    className={appDisqusBem({
                      'reduce-right-padding': !articleUsesSideBlocks
                    })()}
                  />
                ) : (
                  <Feedback className={appFeedbackBem()} />
                ))}
            </main>
            <Footer
              className={appFooterBem()}
              feedbackEmail={feedbackEmail}
              articleUsesSideblocks={articleUsesSideBlocks}
            />
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { app, tree, config, article, sideBlocks } = state;

  const isTocDisabled = Boolean(config.get('hideToc'));

  const feedbackEmail = config.get('webmaster');

  const productName = config.get('productName');
  const isNavigationVisible = app.get('isNavigationVisible');
  const isSearchOpened = app.get('isSearchOpened') || false;

  const articleUsesSideBlocks = sideBlocks && Boolean(sideBlocks.size);
  const articleIsReady =
    !article.get('hasErrorOccurred') && !article.get('isLoading');

  const selectedPageId = tree.get('selectedPageId');
  const selectedPageExistsInTOC =
    (selectedPageId &&
      state.tree.hasIn(['entities', 'pages', selectedPageId])) ||
    false;
  const selectedArticleTitle =
    (selectedPageExistsInTOC &&
      tree.getIn(['entities', 'pages', selectedPageId, 'title'])) ||
    null;
  const isDisqusEnabled =
    config.get('showDisqus') &&
    (selectedPageExistsInTOC || Boolean(article.get('fallbackDiscussId')));

  return {
    productName,
    isNavigationVisible,
    isSearchOpened,
    isDisqusEnabled,
    articleIsReady,
    articleUsesSideBlocks,
    selectedArticleTitle,
    selectedPageId,
    feedbackEmail,
    isTocDisabled
  };
};

const mapDispatchToProps = dispatch => ({
  setMobileMode: enabled => dispatch(setMobileMode(enabled))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WebHelp);
