import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import queryString from 'query-string';

import ArticleError from './article-error.jsx';

import { selectNode } from 'actions/toc-tree';
import {
  articleScrollStated,
  articleScrollEnded,
  startFetchingArticle,
  reportArticleFetchCompleted,
  reportArticleFetchError,
  setArticleFallbackDisqusId,
  setSectionSwitcherLabel
} from 'actions/article';
import { changeSection } from 'actions/app';
import { setAvailableSections } from 'actions/config';

import { convertArticleToReact } from 'utils/article-to-jsx';

import { articleBem, articleContentBem, articleSidePanelBem } from './_bem';
import { SCROLL_DURATION, SCROLL_TOP_EDGE_OFFSET } from 'constants/scroll';
import { getCumulativeOffsetTop } from 'utils/get-cumulative-offest';
import SidePanel from 'containers/side-panel';
import { clearSideBlocks } from 'actions/side-blocks';

const CUSTOM_LABEL = 'data-sections-label';

class Article extends Component {
  static propTypes = {
    selectNode: PropTypes.func,

    /* router state */
    match: PropTypes.object,
    history: PropTypes.object,
    location: PropTypes.object,

    /* scrolling */
    onScrollStart: PropTypes.func,
    onScrollEnd: PropTypes.func,
    scrollToElement: PropTypes.func,
    scrollToY: PropTypes.func,

    /* Fetching state */
    articleFetchStarted: PropTypes.func,
    articleFetchCompleted: PropTypes.func,
    articleFetchFailed: PropTypes.func,
    hasErrorOccurred: PropTypes.bool,
    isLoading: PropTypes.bool,
    setSectionSwitcherLabel: PropTypes.func,

    setAvailableSections: PropTypes.func,
    changeSection: PropTypes.func,

    isDisqusEnabled: PropTypes.bool,
    setFallbackDisqusId: PropTypes.func,

    usesSideBlocks: PropTypes.bool,

    clearSideBlocks: PropTypes.func
  };

  constructor(props) {
    super(props);
    this.state = {};
    const wrapper = document.querySelector('.wrapper');
    const articleNode =
      wrapper instanceof HTMLElement && wrapper.querySelector('.article');

    if (articleNode) {
      this.props.clearSideBlocks();
      this.state.articleJSX = convertArticleToReact(articleNode.innerHTML);
      this.props.articleFetchCompleted();
      wrapper.parentNode.removeChild(wrapper);
    }
  }

  componentDidMount() {
    const { articleJSX } = this.state;
    const bodyElm = document.querySelector('body');
    const articleId = bodyElm.getAttribute('data-id');
    const anchor = this.props.location.hash;

    if (!articleJSX) {
      const { articlePath } = this.props.match.params;
      const articleUrl = articlePath.split('/').pop();

      this.fetchArticle(articleUrl);
    }

    if (articleId && anchor) {
      this.props.selectNode(articleId + anchor);
      // strange way to wait until scrollable parent is mounted
      setTimeout(() => this.scrollToAnchor(anchor), 0);
    } else if (articleId) {
      this.props.selectNode(articleId);
    }

    this.checkSections(bodyElm);
    this.extractFallbackDesqusId(bodyElm);
  }

  componentWillReceiveProps(nextProps) {
    const { articlePath: pathname } = this.props.match.params;
    const { articlePath: nextPathname } = nextProps.match.params;
    const { location } = this.props;
    const { location: nextLocation } = nextProps;

    if (
      pathname === nextPathname &&
      nextLocation.key !== location.key &&
      nextLocation.search === location.search
    ) {
      if (nextLocation.hash) {
        this.scrollToAnchor(nextLocation.hash);
      } else {
        this.scrollToY(0);
      }
    }

    // article changed
    if (pathname !== nextPathname) {
      const articleUrl = nextPathname.split('/').pop();
      this.fetchArticle(articleUrl);
    }
  }

  scrollToAnchor = (anchorId, options = {}) => {
    const { scrollDuration = SCROLL_DURATION } = options;
    const anchorName = anchorId && anchorId.split('#')[1];
    const element =
      document.querySelector(`[data-toc='${anchorId}']`) ||
      document.getElementById(anchorName) ||
      document.querySelector(`a[name='${anchorName}']`);
    if (element) {
      this.props.onScrollStart && this.props.onScrollStart();

      this.props.scrollToY(
        getCumulativeOffsetTop(element, '.app__main') - SCROLL_TOP_EDGE_OFFSET,
        scrollDuration
      );

      setTimeout(() => {
        this.props.onScrollEnd && this.props.onScrollEnd();
      }, scrollDuration);
    }
  };

  scrollToY = (y, options = {}) => {
    const { scrollDuration = SCROLL_DURATION } = options;
    this.props.onScrollStart && this.props.onScrollStart();
    this.props.scrollToY(y, scrollDuration);
    setTimeout(() => {
      this.props.onScrollEnd && this.props.onScrollEnd();
    }, scrollDuration);
  };

  fetchArticle = url => {
    this.props.articleFetchStarted();
    return fetch(url)
      .then(response => {
        if (!response.ok) {
          return Promise.reject(new Error(response.statusText));
        }
        return response.text();
      })
      .then(xmlString => {
        const domParser = new DOMParser();
        return domParser.parseFromString(xmlString, 'text/html');
      })
      .then(documentNode => {
        const articleId = documentNode
          .querySelector('body')
          .getAttribute('data-id');
        const anchor = this.props.location.hash;
        const articleNode = documentNode.querySelector('article') || null;

        if (articleId && anchor) {
          this.props.selectNode(articleId + anchor);
        } else if (articleId) {
          this.props.selectNode(articleId);
        }

        if (articleNode) {
          const bodyElm = documentNode.querySelector('body');
          this.checkSections(bodyElm);
          this.extractFallbackDesqusId(bodyElm);
          this.props.clearSideBlocks();
          this.setState(
            {
              articleJSX: convertArticleToReact(articleNode.innerHTML)
            },
            () => {
              this.props.articleFetchCompleted();
              if (anchor) {
                // bad way to wait until all images are loaded
                // eslint-disable-next-line
                setTimeout(
                  () => this.scrollToAnchor(anchor, { scrollDuration: 0 }),
                  500
                );
              } else {
                this.scrollToY(0, { scrollDuration: 0 });
              }
            }
          );
        }
      })
      .catch(error => {
        error.message = `${error.message}, Article Url: ${url}`;
        this.props.articleFetchFailed(error);
      });
  };

  checkSections(bodyElm) {
    const availableSectionsStr = bodyElm.getAttribute('data-section-switcher');
    let availableSections = null;

    // store available sections in store, if they're specified in the article body
    if (availableSectionsStr) {
      availableSections = availableSectionsStr
        .substring(1, availableSectionsStr.length - 1)
        .split(';');
      this.props.setAvailableSections(availableSections);
    } else {
      this.props.setAvailableSections([]);
    }

    const isCustomSectionSwitcherLabel = bodyElm.hasAttribute(CUSTOM_LABEL);
    if (isCustomSectionSwitcherLabel) {
      this.props.setSectionSwitcherLabel(bodyElm.getAttribute(CUSTOM_LABEL));
    }

    // if a query contains a pre-selected section, use it, and fallback
    // to a first section (if there is one) specified in the body, if not.
    const maybeSection = queryString.parse(location.search).section;

    if (maybeSection) {
      const sectionIsAvailable =
        availableSections &&
        availableSections.filter(i => i === maybeSection).length;

      if (sectionIsAvailable) {
        this.props.changeSection(maybeSection);
      } else if (availableSections && availableSections.length) {
        this.props.changeSection(availableSections[0]);
      }
    } else if (availableSections && availableSections.length) {
      this.props.changeSection(availableSections[0]);
    }
  }

  extractFallbackDesqusId = bodyElm => {
    const { setFallbackDisqusId } = this.props;
    const fallbackDiscussId = bodyElm.getAttribute('data-disqus-id');
    setFallbackDisqusId && setFallbackDisqusId(fallbackDiscussId);
  };

  render() {
    const { articleJSX } = this.state;
    const { isLoading, hasErrorOccurred, usesSideBlocks } = this.props;

    if ((!isLoading && !articleJSX) || hasErrorOccurred) {
      return (
        <ArticleError isLoading={isLoading} usesSideblocks={usesSideBlocks} />
      );
    }

    return (
      <article
        className={articleBem({
          loading: isLoading,
          'without-sideblocks': !usesSideBlocks
        })()}
      >
        <SidePanel className={articleSidePanelBem()} />
        <div className={articleContentBem()}>{articleJSX}</div>
      </article>
    );
  }
}

const mapStateToProps = state => {
  const isLoading = state.article.get('isLoading');
  const hasErrorOccurred = state.article.get('hasErrorOccurred');
  const usesSideBlocks = state.sideBlocks.size > 0;

  return {
    isLoading,
    hasErrorOccurred,
    usesSideBlocks
  };
};

const mapDispatchToProps = dispatch => ({
  onScrollStart: () => dispatch(articleScrollStated()),
  onScrollEnd: () => dispatch(articleScrollEnded()),

  articleFetchStarted: () => dispatch(startFetchingArticle()),
  articleFetchCompleted: () => dispatch(reportArticleFetchCompleted()),
  articleFetchFailed: error => dispatch(reportArticleFetchError(error)),

  selectNode: selectedNodeId => dispatch(selectNode(selectedNodeId)),

  setSectionSwitcherLabel: label => dispatch(setSectionSwitcherLabel(label)),

  setAvailableSections: list => dispatch(setAvailableSections(list)),
  changeSection: section => dispatch(changeSection(section)),

  setFallbackDisqusId: fallbackDisqusId =>
    dispatch(setArticleFallbackDisqusId(fallbackDisqusId)),

  clearSideBlocks: () => dispatch(clearSideBlocks())
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Article)
);
