import queryString from 'query-string';

import { history } from 'utils/history';
import { isSearchAvailable } from 'utils/is-search-available';
import {
  applyParamsToQueryString,
  removeParamFromQueryString
} from 'utils/querystring-helpers';

import * as Type from 'constants/action-types';

import { clearSearch, performSearch } from 'actions/search';
import { hideSearch, showSearch } from 'actions/app';

/* eslint no-unused-vars: ["error", { "args": "none" }] */
export const syncSearchWithUrl = store => {
  let prevQuery;

  history.listen(location => {
    const currentQuery = queryString.parse(location.search).q;

    if (!prevQuery && currentQuery) {
      store.dispatch(performSearch(currentQuery));
      store.dispatch(showSearch());
    } else if (!currentQuery && prevQuery) {
      store.dispatch(clearSearch());
      store.dispatch(hideSearch());
    }

    prevQuery = queryString.parse(location.search).q;
  });

  return next => action => {
    const result = next(action);
    const currentQueryInUrl = queryString.parse(history.location.search).q;

    /* eslint default-case: off */
    switch (action.type) {
      case Type.CONFIG_FETCH_SUCCESS:
        const queryParams = queryString.parse(location.search);

        if (queryParams.q && isSearchAvailable(action.data)) {
          store.dispatch(performSearch(queryParams.q));
          store.dispatch(showSearch());
        }

        break;

      case Type.SEARCH_RESULTS_READY:
        if (currentQueryInUrl !== action.query) {
          applyParamsToQueryString({ q: action.query });
        }

        break;

      case Type.CLEAR_SEARCH:
        if (currentQueryInUrl) {
          removeParamFromQueryString('q');
        }

        break;
    }

    return result;
  };
};
