import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { convertNodeToElement } from 'react-html-parser';
import { replacer } from 'utils/article-to-jsx';

class Sticker extends Component {
  static propTypes = {
    onHeightChanged: PropTypes.func,
    sideBlock: PropTypes.object,
    content: PropTypes.object
  };

  componentDidMount() {
    window.requestAnimationFrame(this.checkAndUpdateHeight);
  }

  checkAndUpdateHeight = () => {
    const newHeight = this.stickerRef && this.stickerRef.scrollHeight;

    if (this.height !== newHeight) {
      newHeight && this.props.onHeightChanged(newHeight);
      this.height = newHeight;
    }

    window.requestAnimationFrame(this.checkAndUpdateHeight);
  };

  handleRef = ref => {
    this.stickerRef = ref;
  };

  render() {
    const { sideBlock } = this.props;
    const content = sideBlock.get('content');

    return (
      <div
        ref={this.handleRef}
        style={{
          position: 'absolute',
          left: 0,
          right: 0, // TODO: extract to styles file
          top: sideBlock.get('desiredOffsetTop'),
          visibility: !sideBlock.get('desiredOffsetTop') && 'hidden'
        }}
      >
        {content &&
          convertNodeToElement(content, sideBlock.get('id'), replacer)}
      </div>
    );
  }
}

export default Sticker;
