import { connect } from 'react-redux';
import SidePanel from 'components/side-panel';
import { setStickersRelativeBlocksOffsets } from '../actions/side-blocks';

const mapStateToProps = state => ({
  sideBlocks: state.sideBlocks
});

const mapDispatchToProps = dispatch => ({
  setStickersRelativeBlocksOffsets: offsets =>
    dispatch(setStickersRelativeBlocksOffsets(offsets))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SidePanel);
