import 'theme';

import 'core-js/es6/map';
import 'core-js/es6/set';
import 'raf/polyfill';
import 'classlist-polyfill';

import React from 'react';
import { render } from 'react-dom';

import App from './app.jsx';
import NotSupportedBrowserPage from 'components/not-supported-browser-page';

import defineHtmlElement from 'utils/define-html-element';
import defineMetaViewport from 'utils/define-meta-viewport';
import { history } from 'utils/history';

import testFlexbox from 'utils/test-flexbox-support';

const isBrowserSupported = testFlexbox() && window.matchMedia;
const NEW_RELIC = window.newrelic || window.NREUM;

/* eslint no-undef: "off" */
if (NEW_RELIC && BUILD_NUMBER) {
  NEW_RELIC.addRelease('WebHelp', BUILD_NUMBER);
}

document.head.appendChild(
  defineMetaViewport({
    width: 'device-width',
    initialScale: 1,
    maximumScale: 1
  })
);

document.addEventListener('DOMContentLoaded', () => {
  render(
    isBrowserSupported ? (
      <App history={history} />
    ) : (
      <NotSupportedBrowserPage />
    ),
    defineHtmlElement('#root.layout'),
    () => {
      document.body.classList.add('app-is-rendered');
    }
  );
});
