import React from 'react';
import PropTypes from 'prop-types';

import Footer from 'components/footer';
import { layoutContainerBem, layoutContentBem, layoutFooterBem } from './_bem';

export const LayoutContainer = ({ className, children, withFooter }) => {
  if (withFooter === true) {
    // prettier-ignore
    return (
      <div className={ layoutContainerBem({direction: 'column'}).mix(className)() }>
        <div className={ layoutContentBem() }>
          { children }
        </div>
        <Footer className={layoutFooterBem()} />
      </div>
    );
  }

  return <div className={layoutContainerBem.mix(className)()}>{children}</div>;
};

LayoutContainer.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any.isRequired,
  withFooter: PropTypes.bool
};
