import React, { Component } from 'react';
import debounce from 'utils/debounce';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import searchGlyph from 'theme/icons/search.svg';

import {
  searchOverlayBem,
  searchOverlayInputBem,
  searchOverlayInputWrapperBem,
  searchOverlayResultsBem,
  searchOverlayResultBem,
  searchOverlayTitleBem,
  searchOverlayLinkBem,
  searchOverlaySnippetBem,
  searchOverlayButtonBem,
  searchOverlayButtonGlyphBem,
  searchOverlayLoaderBem
} from './_bem';

const DEBOUNCE_TIMEOUT = 250;

export default class SearchOverlay extends Component {
  static propTypes = {
    performSearch: PropTypes.func.isRequired,
    clearSearch: PropTypes.func.isRequired,
    waitingForResults: PropTypes.bool,
    results: PropTypes.object,
    query: PropTypes.string,
    onClose: PropTypes.func
  };

  static getNoResultsWarning(query) {
    return query ? `No results matching "${query}" were found` : `No results`;
  }

  constructor(props) {
    super(props);

    const { query } = this.props;
    this.debouncedPerformSearch = debounce(
      props.performSearch,
      DEBOUNCE_TIMEOUT
    );
    this.state = {
      query: query || '' // prevent react warning about switching from uncontrolled to controlled state
    };
  }

  componentDidMount() {
    this.inputRef.focus();
    document.addEventListener('keyup', this.handleDocumentKeyUp);
  }

  componentWillUnmount() {
    document.removeEventListener('keyup', this.handleDocumentKeyUp);
  }

  handlePerformSearch = () => {
    const { query } = this.state;
    query && this.props.performSearch(query); // prevent searching empty string because it's closing search-overlay
  };

  handleDocumentKeyUp = e => {
    if (e.key === 'Escape') {
      this.props.onClose();
    }
  };

  handleInputKeyPress = e => {
    if (e.key === 'Enter') {
      this.props.performSearch(this.state.query);
    }
  };

  handleQueryChange = evt => {
    const query = evt.target.value;
    this.setState({ query });
    const trimedQuery = query.trim();
    trimedQuery && this.debouncedPerformSearch(trimedQuery); // prevent searching empty string because it's closing search-overlay
  };

  renderSearchResult = (result, index) => {
    const { onClose } = this.props;

    return (
      <div key={index} className={searchOverlayResultBem()}>
        <Link
          className={searchOverlayTitleBem()}
          onClick={onClose}
          to={result.get('relativeUrl')}
        >
          {result.get('title')}
        </Link>
        <Link
          className={searchOverlayLinkBem()}
          onClick={onClose}
          to={result.get('relativeUrl')}
        >
          {result.get('absoluteUrl')}
        </Link>
        <div
          className={searchOverlaySnippetBem()}
          dangerouslySetInnerHTML={{ __html: result.get('snippet') }}
        />
      </div>
    );
  };

  render() {
    const { results, waitingForResults, query } = this.props;

    return (
      <div className={searchOverlayBem()}>
        <div className={searchOverlayInputWrapperBem()}>
          <input
            className={searchOverlayInputBem()}
            value={this.state.query}
            onChange={this.handleQueryChange}
            onKeyPress={this.handleInputKeyPress}
            ref={ref => (this.inputRef = ref)}
            placeholder="Search"
            data-hj-whitelist
          />
          <span className={searchOverlayButtonBem()}>
            <svg className={searchOverlayButtonGlyphBem()}>
              <use xlinkHref={`#${searchGlyph.id}`} />
            </svg>
          </span>
          <div
            className={searchOverlayLoaderBem({ loading: waitingForResults })()}
          />
        </div>
        <div
          className={searchOverlayResultsBem({
            'no-results': results.size === 0
          })()}
        >
          {results.size > 0
            ? results.map(this.renderSearchResult)
            : SearchOverlay.getNoResultsWarning(query)}
        </div>
      </div>
    );
  }
}
