import { connect } from 'react-redux';
import { Waypoint } from 'react-waypoint';
import { selectAnchorNode, selectPrevAnchorNode } from 'actions/toc-tree';

const { below, inside, above } = Waypoint;
let waypointsQueue = [];
let page = '';

const mapDispatchToProps = (dispatch, { anchorId }) => ({
  onPositionChange: ({ currentPosition, previousPosition }) => {
    const isNewPageLoaded = page !== anchorId.split('#')[0];
    if (isNewPageLoaded) {
      waypointsQueue = [];
      page = anchorId.split('#')[0];
    }
    const isScrollingDown =
      !previousPosition ||
      (currentPosition === above && previousPosition === inside) ||
      (currentPosition === inside && previousPosition === below);
    const waypointIsInViewport = currentPosition === inside;
    const pushMethod = isScrollingDown ? 'push' : 'unshift';
    const popMethod = isScrollingDown ? 'shift' : 'pop';
    waypointIsInViewport
      ? waypointsQueue[pushMethod](anchorId)
      : waypointsQueue[popMethod]();
    if (!waypointsQueue[0] && isScrollingDown) {
      return;
    }
    dispatch(
      waypointsQueue[0]
        ? selectAnchorNode(waypointsQueue[0])
        : selectPrevAnchorNode(anchorId)
    );
  }
});

const mergeProps = (stateProps, dispatchProps, ownProps) => ({
  fireOnRapidScroll: true,
  bottomOffset: '10%',
  topOffset: '0%',
  ...ownProps,
  ...stateProps,
  ...dispatchProps
});

export default connect(
  null,
  mapDispatchToProps,
  mergeProps
)(Waypoint);
