import createElement from 'create-element-by-selector.js';

/**
 * @description Find HTMLElement in document or create and append to the document if it don't exist.
 * @param {String} selector
 * @return {HTMLElement}
 */
const defineHtmlElement = selector => {
  let element = document.querySelector(selector);

  if (!element) {
    element = createElement(selector);
    document.body.appendChild(element);
  }

  return element;
};

export default defineHtmlElement;
