import { connect } from 'react-redux';
import SearchOverlay from 'components/search-overlay';
import { performSearch, clearSearch } from 'actions/search';

const mapStateToProps = state => ({
  results: state.search.get('results'),
  query: state.search.get('query'),
  waitingForResults: state.search.get('waitingForResults')
});

const mapDispatchToProps = dispatch => ({
  performSearch: query => dispatch(performSearch(query)),
  clearSearch: () => dispatch(clearSearch())
});

export default connect(mapStateToProps, mapDispatchToProps)(SearchOverlay);
