import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { List } from 'immutable';

import {
  dropdownContentBem,
  dropdownListBem,
  dropdownListLinkBem
} from './_bem.js';

class Content extends Component {
  componentDidMount() {
    const { currentItem } = this.props;

    if (currentItem) {
      const { wrapper } = this.refs;
      const currentItemNode = this.refs[`item-${currentItem}`];
      if (!currentItemNode) {
        return;
      }

      const wrapperRect = wrapper.getBoundingClientRect();
      const currentItemRect = currentItemNode.getBoundingClientRect();
      const wrapperTop = wrapperRect.top || wrapperRect.y;
      const currentItemTop = currentItemRect.top || currentItemRect.y;
      const currentItemBottom = currentItemTop + currentItemRect.height;

      if (
        currentItemBottom - wrapperTop > wrapperRect.height ||
        currentItemTop - wrapperTop < 0
      ) {
        wrapper.scrollTop = currentItemTop - wrapperTop;
      }
    }
  }

  render() {
    const { items, onChange, currentItem } = this.props;
    return (
      <div className={dropdownContentBem()} ref="wrapper">
        <ul className={dropdownListBem()}>
          {items.map(item => {
            const itemId = item.get('id');
            const isActive = item.get('id') === currentItem;
            return (
              <li
                key={itemId}
                data-item={itemId}
                ref={`item-${itemId}`}
                className={dropdownListLinkBem({ active: isActive })()}
                onClick={() => onChange(itemId)}
              >
                {item.get('name')}
              </li>
            );
          })}
        </ul>
      </div>
    );
  }
}

Content.propTypes = {
  items: PropTypes.instanceOf(List),
  currentItem: PropTypes.string,
  onChange: PropTypes.func
};

export default Content;
