import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { List } from 'immutable';

import { DropDown, Trigger, Content } from 'components/dropdown';
import * as appActions from 'actions/app';

import { keymapSwitcherBem } from './_bem.js';

import { applyParamsToQueryString } from 'utils/querystring-helpers';

const LABEL = 'Shortcuts';

const KeymapSwitcher = ({ currentKeymap, allKeymaps, changeKeymap }) => {
  return (
    <div className={keymapSwitcherBem()}>
      <DropDown
        items={allKeymaps}
        currentItem={currentKeymap}
        label={LABEL}
        align="right"
        onChange={newKeymap => {
          changeKeymap(newKeymap);
          applyParamsToQueryString({ keymap: newKeymap });
        }}
      >
        <Trigger />
        <Content />
      </DropDown>
    </div>
  );
};

KeymapSwitcher.propTypes = {
  currentKeymap: PropTypes.string.isRequired,
  allKeymaps: PropTypes.instanceOf(List).isRequired,
  changeKeymap: PropTypes.func.isRequired
};

const mapStateToProps = state => {
  const allKeymaps = state.config.getIn(['keymaps', 'all']);
  return {
    currentKeymap: state.app.get('currentKeymap'),
    allKeymaps: allKeymaps
  };
};

const mapDispatchToProps = dispatch => {
  return {
    changeKeymap: keymap => dispatch(appActions.changeKeymap(keymap))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(KeymapSwitcher);
