import { normalize, schema } from 'normalizr';

export default function normalizeTreeData(treeData) {
  let tabIndex = 10;

  const anchorSchema = new schema.Entity('anchors');
  const anchorsSchema = new schema.Array(anchorSchema);

  const pageSchema = new schema.Entity(
    'pages',
    {},
    {
      idAttribute: value => value.id || `autogen_id_${tabIndex}`,
      mergeStrategy: (entityA, entityB) => {
        if (entityA.url !== entityB.url) {
          console.error(
            `duplicate id with not matching urls`,
            entityA,
            entityB
          );
        } else {
          console.warn(`duplicate id with matching urls`, entityA, entityB);
        }
        return entityB;
      },
      processStrategy: (value, parent) => {
        return {
          ...value,
          id: value.id || `autogen_id_${tabIndex + 1}`, // done using "kostyil&velosipied" methodology, should be removed when ids will appear in all nodes
          parentId: parent.id,
          level: !!parent.id ? parent.level + 1 : 0,
          tabIndex: tabIndex++
        };
      }
    }
  );

  const pagesSchema = new schema.Array(pageSchema);
  pageSchema.define({ pages: pagesSchema, anchors: anchorsSchema });

  const isArray = treeData instanceof Array;
  let _treeData;
  if (!isArray) {
    _treeData = [treeData];
  } else {
    _treeData = treeData;
  }
  const normalizedData = normalize(_treeData, pagesSchema);

  normalizedData.topLevelIds = normalizedData.result;

  delete normalizedData.result;

  return normalizedData;
}
