import React from 'react';
import { Link } from 'react-router-dom';

import { LayoutContainer } from 'components/layout';
import JetBrainsLogo from 'components/jetbrains-logo';

import pageData from './youtrack-home-page-data';

const YouTrackHome = () => (
  <LayoutContainer withFooter>
    <div className="youtrack-home-page">
      <h1 className="_big">
        <JetBrainsLogo logo="youtrack" size="1" inline="left" />
        YouTrack documentation
      </h1>
      <div className="youtrack-home-page__row">
        {pageData.map(({ title, path = '', links }) => (
          <div className="youtrack-home-page__col " key={title}>
            <h2>{title}</h2>
            <ul className="list _no-style">
              {links.map(({ text, url, externalUrl }) => (
                <li className="list__item" key={text}>
                  {url && <Link to={`${path}${url}`}>{text}</Link>}
                  {externalUrl && <a href={externalUrl}>{text}</a>}
                </li>
              ))}
            </ul>
          </div>
        ))}
      </div>
    </div>
  </LayoutContainer>
);

export default YouTrackHome;
