import { fetchTreeData } from 'actions/toc-tree';
import { TOC_URL } from 'constants/url';
import * as Type from '../constants/action-types';
import { history } from 'utils/history';

export const tocMiddleware = store => next => action => {
  const { tree } = store.getState();
  if (action.type === Type.SELECT_NODE && tree.get('treeDataReady')) {
    const pageId = action.nodeId;
    const page = tree.getIn(['entities', 'pages', pageId]);

    if (page && page.get('selectFirstChildOnClick')) {
      // go deeper until we found actual page with url
      let currentPage = page;
      while (currentPage && currentPage.get('selectFirstChildOnClick')) {
        const firstChildId = currentPage.getIn(['pages', 0]);
        currentPage = tree.getIn(['entities', 'pages', firstChildId]);
      }

      if (currentPage && currentPage.get('id')) {
        action.nodeId = currentPage.get('id');
        history.push(currentPage.get('url'));
      }
    }
  }

  const result = next(action);

  if (action.type === Type.CONFIG_FETCH_SUCCESS) {
    store.dispatch(fetchTreeData(TOC_URL));
  }

  return result;
};
