import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { List, Map } from 'immutable';

import { DropDown, Trigger, Content } from 'components/dropdown';
import * as appActions from 'actions/app';

import { sectionSwitcherBem } from './_bem.js';

const SectionSwitcher = ({
  currentSection,
  allSections,
  changeSection,
  switcherLabel
}) => (
  <div className={sectionSwitcherBem()}>
    <DropDown
      items={List(
        allSections.map(name => {
          return Map({ id: name, name });
        })
      )}
      currentItem={currentSection || allSections.get(0)}
      label={switcherLabel}
      align="right"
      onChange={changeSection}
    >
      <Trigger />
      <Content />
    </DropDown>
  </div>
);

SectionSwitcher.propTypes = {
  currentSection: PropTypes.string,
  allSections: PropTypes.instanceOf(List).isRequired,
  changeSection: PropTypes.func.isRequired,
  switcherLabel: PropTypes.string
};

SectionSwitcher.defaultProps = {
  switcherLabel: 'Section'
};

const mapStateToProps = state => ({
  currentSection: state.app.get('currentSection'),
  allSections: state.config.get('sections'),
  switcherLabel: state.article.get('sectionSwitcherLabel')
});

const mapDispatchToProps = dispatch => {
  return {
    changeSection: section => dispatch(appActions.changeSection(section))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SectionSwitcher);
