import * as Type from 'constants/action-types';
import { Map } from 'immutable';

const initialArticle = Map({
  isScrolling: false,
  isLoading: false,
  hasErrorOccurred: false,
  sectionSwitcherLabel: 'Section'
});

export default function article(state = initialArticle, action) {
  switch (action.type) {
    case Type.ARTICLE_FETCH_START:
      return state.set('isLoading', true);

    case Type.ARTICLE_FETCH_COMPLETED:
      return state.withMutations(mutableState =>
        mutableState.set('isLoading', false).set('hasErrorOccurred', false)
      );

    case Type.ARTICLE_FETCH_FAILED:
      return state.withMutations(mutableState =>
        mutableState.set('isLoading', false).set('hasErrorOccurred', true)
      );

    case Type.ARTICLE_SCROLL_TO_ANCHOR_STARTED:
      return state.set('isScrolling', true);

    case Type.ARTICLE_SCROLL_TO_ANCHOR_ENDED:
      return state.set('isScrolling', false);

    case Type.ARTICLE_SET_FALLBACK_DISQUS_ID:
      return state.set('fallbackDiscussId', action.fallbackDiscussId);

    case Type.SELECT_SECTIONS_LABEL:
      return state.set('sectionSwitcherLabel', action.label);

    default:
      return state;
  }
}
