import { List, fromJS } from 'immutable';
import * as Type from 'constants/action-types';

import { handleError } from 'actions/error';
import { suggestKeymap } from 'actions/app';

function configFetchStart(url) {
  return {
    type: Type.CONFIG_FETCH_START,
    url
  };
}

function configFetchSuccess(data) {
  return {
    type: Type.CONFIG_FETCH_SUCCESS,
    data: data
  };
}

export function setAvailableSections(list) {
  return {
    type: Type.SET_AVAILABLE_SECTIONS,
    list
  };
}

export function fetchConfig(url) {
  return dispatch => {
    dispatch(configFetchStart(url));

    fetch(url)
      .then(response => {
        if (!response.ok) {
          throw new Error(response.statusText);
        }
        return response.json();
      })
      .then(config => dispatch(configFetchSuccess(config)))
      .then(action => {
        const maybeKeymaps = fromJS(action.data).get('keymaps');
        if (maybeKeymaps) {
          dispatch(
            suggestKeymap({
              allPrimary:
                maybeKeymaps.getIn(['primary', 'values']) ||
                maybeKeymaps.get('values') ||
                new List(),
              defaultPrimary:
                maybeKeymaps.getIn(['primary', 'default']) ||
                maybeKeymaps.get('default'),
              allSecondary:
                maybeKeymaps.getIn(['secondary', 'values']) || new List(),
              defaultSecondary:
                maybeKeymaps.getIn(['secondary', 'default']) || 'default'
            })
          );
        } else {
          throw new Error('No keymaps data was found in config');
        }

        return action;
      })
      .catch(error => dispatch(handleError(error, 'config')));
  };
}
