import React, { Component } from 'react';

import { tocPreloaderBem, tocPreloaderLineBem } from './_bem';

import './toc-preloader.scss';

const PRELOADER_BLOCKS = 2;
const PRELOADER_LINES_IN_BLOCK = 4;

const preloaderLength = PRELOADER_BLOCKS * PRELOADER_LINES_IN_BLOCK;

// this prevents us from the `requestAnimationFrame is not defined` error thrown when
// `requestAnimationFrame` is not in the global scope
const requestAnimationFrame = window.requestAnimationFrame || false;

class TocPreloader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // if requestAnimationFrame is defined, we'll set isVisible to true later, on componentDidMount
      isVisible: !requestAnimationFrame
    };
  }

  componentDidMount() {
    if (requestAnimationFrame) {
      requestAnimationFrame(() => {
        this.setState({
          isVisible: true
        });
      });
    }
  }

  componentWillUnmount() {
    this.setState({
      isVisible: false
    });
  }

  render() {
    const { isVisible } = this.state;

    const PreloaderLine = ({ count }) => (
      <div className={tocPreloaderLineBem({ count })()} />
    );

    return (
      <div className={tocPreloaderBem({ visible: isVisible })()}>
        {new Array(preloaderLength).fill(preloaderLength).map((_, i) => (
          <PreloaderLine count={(i % PRELOADER_LINES_IN_BLOCK) + 1} key={i} />
        ))}
      </div>
    );
  }
}

export default TocPreloader;
