import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { findDOMNode } from 'react-dom';

import NodeTitle from './node-title.jsx';

import angleGlyph from 'theme/icons/angle-down.svg';

import {
  tocTreeNodeBem,
  tocTreeExpanderBem,
  tocTreeExpanderGlyphBem,
  tocTreeNodesListBem,
  tocAnchorsListBem,
  tocAnchorBem,
  tocTreeTitleWrapperBem,
  tocAnchorTitleBem
} from './_bem';

export default class TocNode extends PureComponent {
  static propTypes = {
    id: PropTypes.string.isRequired,
    pages: PropTypes.object.isRequired,
    selectedPath: PropTypes.oneOfType([PropTypes.array, PropTypes.bool])
      .isRequired,
    hidden: PropTypes.bool,
    scrollIntoView: PropTypes.func,
    onSelect: PropTypes.func,
    filteredIds: PropTypes.array,
    linkComponent: PropTypes.func,
    selectedSection: PropTypes.string
  };

  static defaultProps = {
    selectedSection: ''
  };

  state = { isExpanded: !!this.props.selectedPath };

  componentDidMount() {
    if (this.isSelected) {
      setTimeout(
        () => this.props.scrollIntoView(findDOMNode(this.nodeTitleRef)),
        50
      );
    }
  }

  get isSelected() {
    const { selectedPath, id } = this.props;
    if (!selectedPath) {
      return false;
    }
    const isAnchorSelected =
      selectedPath[selectedPath.length - 1].indexOf('#') > -1;
    const i = isAnchorSelected ? 2 : 1;
    return selectedPath[selectedPath.length - i] === id;
  }

  get page() {
    return this.props.pages[this.props.id];
  }

  handleTitleRef = ref => {
    this.nodeTitleRef = ref;
  };

  handleExpandedToggle = () => {
    this.setState(state => ({
      isExpanded: !state.isExpanded
    }));
  };

  handleNodeClick = () => {
    this.handleExpandedToggle();
    this.props.onSelect(this.props.id);
  };

  renderChild = id => {
    const { scrollIntoView, selectedPath, filteredIds } = this.props;
    if (filteredIds && !filteredIds.includes(id)) {
      return null;
    }
    return (
      <TocNode
        {...this.props}
        key={id}
        id={id}
        scrollIntoView={scrollIntoView}
        selectedPath={selectedPath && selectedPath.includes(id) && selectedPath}
      />
    );
  };

  renderAnchor = id => {
    const {
      onSelect,
      pages,
      selectedPath,
      linkComponent,
      selectedSection
    } = this.props;
    const anchor = pages[id];
    const isInHiddenSection =
      anchor.section && !anchor.section.includes(selectedSection);
    const isSelected =
      selectedPath && selectedPath[selectedPath.length - 1] === id;
    return (
      <li key={id} className={tocAnchorBem()}>
        <NodeTitle
          ref={this.handleRef}
          page={anchor}
          className={tocAnchorTitleBem({
            [`level_${anchor.level}`]: true
          })()}
          onClick={() => !isSelected && onSelect(id, true)}
          isSelected={isSelected}
          isInHiddenSection={isInHiddenSection}
          linkComponent={linkComponent}
        />
      </li>
    );
  };

  render() {
    const childPages = this.page.pages;
    const anchors = this.page.anchors;
    const level = this.page.level;
    const { filteredIds, linkComponent } = this.props;

    // hide expander if all child pages hidden by filter
    const allChildPagesHidden =
      filteredIds &&
      childPages &&
      !childPages.some(id => filteredIds.includes(id));

    return (
      <li
        className={tocTreeNodeBem.mix(`${tocTreeNodeBem}--level_${level}`)()}
        ref={this.handleTitleRef}
      >
        <div className={tocTreeTitleWrapperBem()}>
          <NodeTitle
            page={this.page}
            isSelected={this.isSelected}
            isChildAnchorSelected={false}
            onClick={this.handleNodeClick}
            linkComponent={linkComponent}
          />
          {childPages && !allChildPagesHidden && (
            <div
              className={tocTreeExpanderBem()}
              onClick={this.handleExpandedToggle}
            >
              <svg
                className={tocTreeExpanderGlyphBem({
                  expanded: this.state.isExpanded
                })()}
              >
                <use xlinkHref={`#${angleGlyph.id}`} />
              </svg>
            </div>
          )}
        </div>
        {this.isSelected && anchors && (
          <ul className={tocAnchorsListBem()}>
            {anchors.map(this.renderAnchor)}
          </ul>
        )}
        {this.state.isExpanded && !allChildPagesHidden && childPages && (
          <ul className={tocTreeNodesListBem()}>
            {childPages.map(this.renderChild)}
          </ul>
        )}
      </li>
    );
  }
}
