import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { List } from 'immutable';

const SectionBlock = ({
  children,
  allSections,
  currentSection,
  correspondingSection
}) => {
  const isCurrent =
    (currentSection || (allSections && allSections[0])) ===
    correspondingSection;

  return isCurrent && children;
};

SectionBlock.propTypes = {
  correspondingSection: PropTypes.string.isRequired,
  currentSection: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
  allSections: PropTypes.instanceOf(List)
};

const mapStateToProps = state => {
  const allSections = state.config.get('sections');
  const currentSection = state.app.get('currentSection', '');
  return {
    allSections,
    currentSection
  };
};

export default connect(mapStateToProps)(SectionBlock);
