import queryString from 'query-string';
// import { tocAnchorBem } from '../containers/toc-tree/_bem';

export const isNotEmpty = link => {
  return !!link.href;
};

export const isNotForTopicId = link => {
  const parsedQuery = queryString.parse(link.search);
  return !('topic' in parsedQuery);
};

export const isNotBypass = link => {
  return !link.getAttribute('data-bypass');
};

export const isNotKeyPressed = ({ altKey, ctrlKey, metaKey, shiftKey }) => {
  return (
    altKey === false &&
    ctrlKey === false &&
    metaKey === false &&
    shiftKey === false
  );
};

export const isNotRightMouseButton = ({ button, which }) => {
  return (button && button !== 2) || (which && which !== 3);
};

export const isNotLightBoxLink = ({ className }) => {
  const classNames = ['lightbox', 'light-box'];
  return classNames.filter(name => className.indexOf(name) !== -1).length === 0;
};

