import React from 'react';
import PropTypes from 'prop-types';

import TocContainer from 'containers/toc';
import { navBem, navTocBem } from './_bem';
import DebouncedFilterInput from 'containers/debounced-filter-input';

const Nav = ({ className, isFilterEnabled }) => (
  <div className={navBem({ 'with-filter': isFilterEnabled }).mix(className)()}>
    {isFilterEnabled && <DebouncedFilterInput />}
    <TocContainer className={navTocBem()} />
  </div>
);

Nav.propTypes = {
  className: PropTypes.string,
  isFilterEnabled: PropTypes.bool
};

export default Nav;
