import { Link } from 'react-router-dom';
import { createLocation } from 'history';
import queryString from 'query-string';

const isModifiedEvent = event =>
  !!(event.metaKey || event.altKey || event.ctrlKey || event.shiftKey);

class PreserveQueryStringLink extends Link {
  static contextTypes = Link.contextTypes;

  handleClick = event => {
    if (this.props.onClick) {
      this.props.onClick(event);
    }

    if (
      !event.defaultPrevented && // onClick prevented default
      event.button === 0 && // ignore everything but left clicks
      !this.props.target && // let browser handle "target=_blank" etc.
      !isModifiedEvent(event) // ignore clicks with modifier keys
    ) {
      event.preventDefault();

      const { history } = this.context.router;
      const { replace, to } = this.props;

      const toLocation =
        typeof to === 'string'
          ? createLocation(to, null, null, history.location)
          : to;

      const targetLink = {
        pathname: toLocation.pathname,
        hash: toLocation.hash,
        search: queryString.stringify(
          Object.assign(
            {},
            queryString.parse(history.location.search),
            queryString.parse(toLocation.search)
          )
        )
      };

      if (replace) {
        history.replace(targetLink);
      } else {
        history.push(targetLink);
      }
    }
  };
}

export { PreserveQueryStringLink as Link };
export default PreserveQueryStringLink;
