import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { findDOMNode } from 'react-dom';
import { List } from 'immutable';

import Trigger from './trigger.jsx';
import Content from './content.jsx';

import { dropdownBem } from './_bem.js';

const DEFAULT_ALIGN = 'right';

class Dropdown extends Component {
  static propTypes = {
    items: PropTypes.instanceOf(List).isRequired,
    label: PropTypes.string,
    currentItem: PropTypes.string,
    onChange: PropTypes.func,
    children: PropTypes.node
  };

  constructor(props) {
    super(props);

    this.state = {
      align: DEFAULT_ALIGN,
      contentVisible: false
    };

    this.checkClicksOutside = this.checkClicksOutside.bind(this);
    this.checkAlignment = this.checkAlignment.bind(this);

    this.onTriggerRef = this.onTriggerRef.bind(this);
    this.onContentRef = this.onContentRef.bind(this);

    this.triggerContent = this.triggerContent.bind(this);
  }

  componentDidMount() {
    window.addEventListener('click', this.checkClicksOutside);
    window.addEventListener('resize', this.checkAlignment);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.checkAlignment);
    window.removeEventListener('click', this.checkClicksOutside);
  }

  triggerContent() {
    this.setState({
      align: this.state.align,
      contentVisible: !this.state.contentVisible
    });
  }

  alignTo(side) {
    this.setState({
      align: side,
      contentVisible: this.state.contentVisible
    });
  }

  onTriggerRef(triggerElm) {
    if (triggerElm) {
      this._triggerElm = triggerElm;
    }
  }

  onContentRef(contentElm) {
    if (contentElm) {
      this._contentElm = contentElm;
    }
  }

  checkAlignment() {
    // check if Content fits the window width and change alignment side, if it's not
    if (this.state.contentVisible && this._contentElm) {
      const contentNode = findDOMNode(this._contentElm);
      const contentRect = contentNode.getBoundingClientRect();

      const windowWidth =
        window.innerWidth ||
        window.document.clientWidth ||
        window.document.body.clientWidth;

      const leftEdgeX = contentRect.left || contentRect.x;
      const rightEdgeX = leftEdgeX + contentRect.width;
      if (rightEdgeX > windowWidth) {
        this.alignTo('right');
      } else if (rightEdgeX + contentRect.width < windowWidth) {
        this.alignTo('left');
      }
    }
  }

  checkClicksOutside(clickEvent) {
    // close Content if click event was fired from outside of the trigger and content
    if (this.state.contentVisible && this._contentElm && this._triggerElm) {
      const contentNode = findDOMNode(this._contentElm);
      const triggerNode = findDOMNode(this._triggerElm);
      const contentRect = contentNode.getBoundingClientRect();
      const triggerRect = triggerNode.getBoundingClientRect();

      const { clientX, clientY } = clickEvent;

      const triggerX = triggerRect.left || triggerRect.x || 0;
      const triggerY = triggerRect.top || triggerRect.y || 0;
      const contentX = contentRect.left || contentRect.x || 0;
      const contentY = contentRect.top || contentRect.y || 0;

      const isOutsideTrigger =
        clientX < triggerX ||
        clientX > triggerX + triggerRect.width ||
        clientY < triggerY ||
        clientY > triggerY + triggerRect.height;

      const isOutsideContent =
        clientX < contentX ||
        clientX > contentX + contentRect.width ||
        clientY < contentY ||
        clientY > contentY + contentRect.height;

      if (isOutsideTrigger && isOutsideContent) {
        this.triggerContent();
      }
    }
  }

  prepareTriggerElm(userTriggerElm) {
    const { label, items } = this.props;
    const currentItemId = this.props.currentItem;
    const currentItemData = items.find(v => v.get('id') === currentItemId);
    const currentItemName = currentItemData
      ? currentItemData.get('name')
      : currentItemId;
    return userTriggerElm ? (
      React.cloneElement(userTriggerElm, {
        ref: this.onTriggerRef,
        onClick: this.triggerContent,
        label: label,
        value: currentItemName
      })
    ) : (
      <Trigger
        ref={this.onTriggerRef}
        onClick={this.triggerContent}
        label={label}
        value={currentItemName}
      />
    );
  }

  prepareContentElm(userContentElm) {
    const { items, onChange, currentItem } = this.props;
    const changeItemAndClose = item => {
      if (onChange) {
        onChange(item);
      }
      this.triggerContent();
    };
    return userContentElm ? (
      React.cloneElement(userContentElm, {
        ref: this.onContentRef,
        items: items,
        onChange: changeItemAndClose,
        currentItem: currentItem
      })
    ) : (
      <Content
        ref={this.onContentRef}
        items={items}
        onChange={changeItemAndClose}
        currentItem={currentItem}
      />
    );
  }

  render() {
    const { children } = this.props;
    const { contentVisible, align } = this.state;
    const childrenArray = React.Children.toArray(children);

    // if (children.length > 2) throw new Error('');

    const triggerElm = this.prepareTriggerElm(
      childrenArray.filter(child => child.type === Trigger)[0]
    );
    const contentElm = this.prepareContentElm(
      childrenArray.filter(child => child.type === Content)[0]
    );

    return (
      <div className={dropdownBem({ opened: contentVisible, align: align })()}>
        {triggerElm}
        {contentVisible && contentElm}
      </div>
    );
  }
}

export default Dropdown;
