export default ({ width, initialScale, maximumScale, userScalable }) => {
  const meta = document.createElement('meta');
  meta.name = 'viewport';

  const content = [];

  if (width) {
    content.push(`width=${width}`);
  }
  if (initialScale) {
    content.push(`initial-scale=${initialScale}`);
  }
  if (maximumScale) {
    content.push(`maximum-scale=${maximumScale}`);
  }
  if (userScalable) {
    content.push(`user-scalable=${userScalable}`);
  }

  meta.content = content.toString();

  return meta;
};
