import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';

import { LayoutContainer } from 'components/layout';
import JetBrainsLogo from 'components/jetbrains-logo';

import rowsData from './home-page-data';

// prettier-ignore
const Home = () => (
  <LayoutContainer withFooter>
    <div className='home-page'>
      <h1>Available resources</h1>
      <p className="add-emphasis">
        This resource is for internal use only and will not appear on production
      </p>

      {rowsData.map(({ title, products }) => (
        <Fragment key={title}>
          <h2 className="margin-large">{title}</h2>
          <div className="home-page__row">
            {products.map(({ title: productTitle, logo, url }) => (
              <div className="home-page__col" key={productTitle}>
                <Link className="home-page__link" to={url}>
                  <JetBrainsLogo
                    className="home-page__link-logo"
                    logo={logo}
                    size="1"
                    align="center"
                  />
                  {productTitle}
                </Link>
              </div>
            ))}
          </div>
        </Fragment>
      ))}
    </div>
  </LayoutContainer>
);

export default Home;
