import React, { Component } from 'react';
import PropTypes from 'prop-types';

import LightboxOverlay from './lightbox-overlay.jsx';
import LightboxContent from './lightbox-content.jsx';

import { lightboxBem } from './_bem';

export default class LightBox extends Component {
  static propTypes = {
    imageSrc: PropTypes.string.isRequired,
    onClose: PropTypes.func
  };

  componentDidMount() {
    document.addEventListener('keyup', this.closeOnEsc);
  }

  componentWillUnMount() {
    document.removeEventListener('keyup', this.closeOnEsc);
  }

  closeOnEsc = e => {
    const { onClose } = this.props;
    const { key, keyCode } = e;
    const isEscapePressed = key === 'Escape' || key === 'Esc' || keyCode === 27;

    if (isEscapePressed && onClose) {
      onClose(e);
    }
  };

  render() {
    const { imageSrc, onClose } = this.props;

    return (
      <div className={lightboxBem()}>
        <LightboxOverlay onClose={onClose} />
        <LightboxContent imageSrc={imageSrc} onClose={onClose} />
      </div>
    );
  }
}
