import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import { showSearch } from 'actions/app';

import {
  articleBem,
  articleErrorIllustrationBem,
  articleErrorMessageBem
} from './_bem';

const ArticleError = ({
  isLoading,
  usesSideBlocks,
  setSearchVisible,
  homePageUrl
}) => (
  <article
    className={articleBem({
      loading: isLoading,
      'has-error': true,
      'without-sideblocks': !usesSideBlocks
    })()}
  >
    <div className={articleErrorIllustrationBem()} />
    <div className={articleErrorMessageBem()}>
      <h4>We can't find the article you're looking for.</h4>
      Check your internet connection and try using{' '}
      <a onClick={setSearchVisible}>search</a>, or start from{' '}
      {homePageUrl ? <Link to={homePageUrl}>home</Link> : `home`}.
    </div>
  </article>
);

ArticleError.propTypes = {
  isLoading: PropTypes.bool,
  usesSideBlocks: PropTypes.bool,
  setSearchVisible: PropTypes.func,
  homePageUrl: PropTypes.string
};

const mapStateToProps = state => {
  const homepageId = state.tree.get('topLevelIds').get(0);

  return {
    homePageUrl: state.tree.getIn(['entities', 'pages', homepageId, 'url'])
  };
};

const mapDispatchToProps = dispatch => ({
  setSearchVisible: () => dispatch(showSearch())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ArticleError);
