import React from 'react';
import PropTypes from 'prop-types';
import {
  tocTreeTitleBem,
  tocTreeNbspTitleWrapperBem,
  tocTreeTitleBadgeBem
} from './_bem';

/* eslint react/prop-types: off, react/no-multi-comp: off*/
const SimpleTitle = ({ page, onClick, className, isInHiddenSection }) => (
  <div className={className} onClick={onClick}>
    {page.title}
    {isInHiddenSection && page.section && (
      <span className={tocTreeNbspTitleWrapperBem()}>
        &nbsp;
        <span className={tocTreeTitleBadgeBem()}>{page.section}</span>
      </span>
    )}
  </div>
);

const LinkTitle = ({ page, to, onClick, className, linkComponent }) => {
  if (linkComponent) {
    const Link = linkComponent;
    return (
      <Link to={to} className={className} onClick={onClick}>
        {page.title}
      </Link>
    );
  }
  return (
    <a href={to} className={className} onClick={onClick}>
      {page.title}
    </a>
  );
};

const NodeTitle = props => {
  const {
    page,
    isSelected,
    isChildAnchorSelected,
    isInHiddenSection,
    className
  } = props;

  if (page.type === 'header') {
    /* eslint no-unused-vars: off */
    const { ...cleanedProps } = props;
    return (
      <SimpleTitle
        {...cleanedProps}
        className={tocTreeTitleBem({ header: true }).mix(className)()}
      />
    );
  }

  if (page.url) {
    const link = page.anchor ? page.url + page.anchor : page.url;
    const TitleType = isInHiddenSection ? SimpleTitle : LinkTitle;
    return (
      <TitleType
        {...props}
        to={link}
        className={tocTreeTitleBem({
          selected: isSelected,
          'have-childs': page.pages && page.pages.length > 0,
          'child-anchor-selected': isChildAnchorSelected,
          disabled: isInHiddenSection,
          link: true
        }).mix(className)()}
      />
    );
  }

  return (
    <SimpleTitle
      {...props}
      className={tocTreeTitleBem({
        selected: isSelected,
        'have-childs': page.pages && page.pages.length > 0,
        'child-anchor-selected': isChildAnchorSelected
      }).mix(className)()}
      page={page}
    />
  );
};

NodeTitle.propTypes = {
  page: PropTypes.object.isRequired,
  onClick: PropTypes.func.isRequired,
  ref: PropTypes.instanceOf(Element),
  isSelected: PropTypes.bool,
  isChildAnchorSelected: PropTypes.bool,
  linkComponent: PropTypes.func,
  className: PropTypes.string
};

export default NodeTitle;
