import { connect } from 'react-redux';

import SideBlock from 'components/side-block';
import { registerSideBlock, unregisterSideBlock } from 'actions/side-blocks';

const mapStateToProps = (state, { id }) => ({
  isMobile: state.app.get('isMobile'),
  positionedOnSidePanel: !!state.sideBlocks.getIn(
    [id, 'desiredOffsetTop'],
    false
  )
});

const mapDispatchToProps = (dispatch, { id, relId, content }) => ({
  registerSideBlock: () =>
    id && relId && dispatch(registerSideBlock(id, relId, content)),
  unregisterSideBlock: () => id && dispatch(unregisterSideBlock(id))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SideBlock);
