import { connect } from 'react-redux';

import Sticker from 'components/side-panel/sticker';
import { stickerHeightChanged } from 'actions/side-blocks';

const mapStateToProps = (state, { id }) => ({
  sideBlock: state.sideBlocks.get(id)
});

const mapDispatchToProps = (dispatch, { id }) => ({
  onHeightChanged: height => dispatch(stickerHeightChanged(id, height))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Sticker);
