import { parsePath } from 'history';
import { parse as parseQueryString } from 'query-string';

export const escapeHtml = unsafe =>
  unsafe
    .replace(/&/g, '&amp;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/"/g, '&quot;')
    .replace(/'/g, '&#039;');

export const hasClass = (parsedDomNode, className) =>
  parsedDomNode.attribs &&
  parsedDomNode.attribs.class &&
  parsedDomNode.attribs.class.indexOf(className) !== -1;

export const isTag = (parsedDomNode, tagName) =>
  parsedDomNode.type === 'tag' && parsedDomNode.name === tagName;

export const hasAttr = (parsedDomNode, attr) =>
  parsedDomNode.type === 'tag' &&
  parsedDomNode.attribs &&
  !!parsedDomNode.attribs[attr];

export const attrsToString = attrs =>
  Object.keys(attrs)
    .map(key => `${key}="${attrs[key]}"`)
    .join(' ');

/* eslint consistent-return: off */
export const nodeToString = (domNode, index) => {
  if (domNode.type === 'text') {
    return escapeHtml(domNode.data);
  } else if (domNode.type === 'tag') {
    return `<${domNode.name} key="${index}" ${attrsToString(
      domNode.attribs
    )}>${domNode.children.map(nodeToString).join('')}</${domNode.name}>`;
  }
};

export const hasQueryStringParam = (href, param) => {
  const parsedPath = parsePath(href);
  const parsedQueryString = parseQueryString(parsedPath.search);
  return param in parsedQueryString;
};
