import { connect } from 'react-redux';
import ReactDisqusThread from 'react-disqus-comments';

class DisqusThread extends ReactDisqusThread {
  shouldComponentUpdate(nextProps) {
    return (
      nextProps.identifier !== this.props.identifier ||
      nextProps.className !== this.props.className
    );
  }
}

const mapStateToProps = ({ config, tree, article }) => {
  const shortname = config.get('disqusID');
  const selectedPageId = tree.get('selectedPageId');
  const identifier =
    tree.getIn(['entities', 'pages', selectedPageId, 'disqus_id']) ||
    article.get('fallbackDiscussId');

  return {
    shortname,
    identifier
  };
};

export default connect(mapStateToProps, () => ({}))(DisqusThread);
