import React, { Component } from 'react';
import PropTypes from 'prop-types';
import consentIds from '@webteam/data-services/lib/privacy-consent-ids';
import bemCn from 'utils/bem-class-name';
import FeedbackPopup from './feedback-popup.jsx';
import { geo, privacy } from '@webteam/data-services';

const cn = bemCn('feedback');

class Feedback extends Component {
  static propTypes = {
    className: PropTypes.string,
    sendFeedback: PropTypes.func,
    isEmailRequired: PropTypes.bool,
    feedbackSupportLink: PropTypes.string
  };

  state = {
    chosenOption: null,
    isPopupShowing: false
  };

  componentDidMount() {
    const { ZENDESK: consentId } = consentIds;

    geo
      .detectCountry()
      .then(({ code: countryCode }) =>
        privacy
          .getMessage(consentId, countryCode)
          .then(label => this.setState({ privacyPolicyMessage: label }))
      );
  }

  handleYesButton = () => {
    this.setState({
      chosenOption: 'yes',
      isPopupShowing: false
    });
  };

  handleNoButton = () => {
    this.setState({
      isPopupShowing: true
    });
  };

  closeFeedbackPopup = ({ chosenOption = null }) => {
    this.setState({
      isPopupShowing: false,
      chosenOption
    });
  };

  render() {
    const { isEmailRequired, feedbackSupportLink, sendFeedback } = this.props;
    const { chosenOption, isPopupShowing, privacyPolicyMessage } = this.state;

    return (
      <div className={cn()}>
        {chosenOption === null && (
          <p>
            Was this page helpful?
            <span
              className={cn('choice-link')()}
              onClick={this.handleYesButton}
            >
              Yes
            </span>
            <span
              className={cn('choice-link')()}
              onClick={this.handleNoButton}
              aria-haspopup="true"
              aria-expanded={isPopupShowing}
            >
              No
            </span>
          </p>
        )}
        {chosenOption !== null && <p>Thanks for your feedback.</p>}
        {chosenOption === 'no' && feedbackSupportLink && (
          <p>
            If you are experiencing a problem or require assistance, please{' '}
            <a href={feedbackSupportLink}>submit a support request</a>.
          </p>
        )}
        {isPopupShowing && (
          <FeedbackPopup
            className={cn('popup')()}
            closeHandler={this.closeFeedbackPopup}
            privacyPolicyMessage={privacyPolicyMessage}
            isEmailRequired={isEmailRequired}
            feedbackSupportLink={feedbackSupportLink}
            sendFeedback={sendFeedback}
          />
        )}
      </div>
    );
  }
}

export default Feedback;
