import { Set } from 'immutable';

export function isExpandable(page) {
  return page && !!page.get('pages');
}

export function getPathToRoot(nodes, nodeId, addSelf = false) {
  const node = nodes.get(nodeId);
  if (!nodeId || !node) {
    // no id no path to root:)
    return Set();
  }

  if (!node.get('parentId')) {
    // root level node
    return addSelf ? Set([nodeId]) : Set();
  }

  const pathToRoot = getPathToRoot(nodes, node.get('parentId'), true);
  return addSelf ? pathToRoot.add(nodeId) : pathToRoot;
}
