import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { keystrokeBem } from './_bem.js';

const HIGHLIGHT_TIMEOUT = 600;
const KEYSTROKE_NOT_AVAILABLE = 'N/A';

class Keystroke extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isFresh: false
    };
  }

  componentDidMount() {
    this.highlight();
  }

  componentWillReceiveProps(nextProps) {
    const { currentKeymap, sourceNode } = this.props;
    const { currentKeymap: nextKeymap } = nextProps;
    const currentKeystroke = sourceNode.attribs[`data-${currentKeymap}`];
    const nextKeystroke = sourceNode.attribs[`data-${nextKeymap}`];

    if (currentKeystroke !== nextKeystroke) {
      this.highlight();
    }
  }

  componentWillUnmount() {
    clearTimeout(this.highlightTimeout);
  }

  highlight = () => {
    this.setState({
      isFresh: true
    });
    this.highlightTimeout = setTimeout(
      () => this.setState({ isFresh: false }),
      HIGHLIGHT_TIMEOUT
    );
  };

  render() {
    const { currentKeymap, sourceNode } = this.props;
    const { isFresh } = this.state;

    const keystroke = currentKeymap
      ? sourceNode.attribs[`data-${currentKeymap}`]
      : sourceNode.children[0].data;

    return (
      <kbd className={keystrokeBem({ fresh: isFresh })()}>
        {keystroke || KEYSTROKE_NOT_AVAILABLE}
      </kbd>
    );
  }
}

Keystroke.propTypes = {
  sourceNode: PropTypes.any.isRequired,
  currentKeymap: PropTypes.string
};

const mapStateToProps = state => ({
  currentKeymap: state.app.get('currentKeymap')
});

export default connect(mapStateToProps)(Keystroke);
