import React from 'react';
import PropTypes from 'prop-types';

import {
  footerBem,
  footerContainerBem,
  footerLogoBem,
  footerCopyrightBem,
  footerFeedbackLinkBem
} from './_bem';

import { JETBRAINS_WEBSITE_URL } from 'constants/url';

const Footer = ({ feedbackEmail, articleUsesSideblocks, className }) => {
  const feedbackUrl = `mailto:${feedbackEmail}`;
  const currentYear = new Date().getFullYear();

  return (
    <footer className={footerBem.mix(className)()}>
      <div
        className={footerContainerBem({
          'reduced-right-padding': !articleUsesSideblocks
        })()}
      >
        <a
          href={JETBRAINS_WEBSITE_URL}
          className={footerLogoBem({ 'without-text': !feedbackUrl })()}
        >
          JetBrains
        </a>
        {feedbackUrl && (
          <a className={footerFeedbackLinkBem()} href={feedbackUrl}>
            Contact us
          </a>
        )}
        <span className={footerCopyrightBem()}>
          Copyright &copy; 2000&ndash;
          {currentYear} JetBrains s.r.o.
        </span>
      </div>
    </footer>
  );
};

Footer.propTypes = {
  feedbackEmail: PropTypes.string,
  articleUsesSideblocks: PropTypes.bool,
  className: PropTypes.string
};

export default Footer;
