import { Set } from 'immutable';
import { getPathToRoot } from './expanding-helpers';

export function filterTree(pages, filteringRegexp) {
  const matchedPagesIds = pages
    .toList()
    .filter(page => filteringRegexp.test(page.get('title')))
    .map(page => page.get('id'));

  // add parent pages to results
  return matchedPagesIds.reduce(
    (matchedPagesWithParents, pageId) =>
      matchedPagesWithParents.union(getPathToRoot(pages, pageId, true)),
    new Set()
  );
}
