import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { findDOMNode } from 'react-dom';

import { createArticleLinkByStage } from './_utils';
import {
  stageWarningBem,
  stageWarningTriggerBem,
  stageWarningIconBem,
  stageWarningContentBem,
  stageWarningLinkBem
} from './_bem';

const ALIGN_LEFT = 'left';
const ALIGN_RIGHT = 'right';
const DEFAULT_ALIGN = ALIGN_LEFT;

class StageWarning extends Component {
  static propTypes = {
    productName: PropTypes.string.isRequired,
    stage: PropTypes.string.isRequired,
    selectedPageId: PropTypes.string.isRequired,
    fallbackUrl: PropTypes.string.isRequired,
    showStageWarningLink: PropTypes.bool
  };

  constructor(props) {
    super(props);

    const { stage, selectedPageId, fallbackUrl } = props;

    this.state = {
      align: DEFAULT_ALIGN,
      isOpened: false,
      link: createArticleLinkByStage(stage, selectedPageId, fallbackUrl)
    };
  }

  componentDidMount() {
    window.addEventListener('resize', this.checkAlignment);
  }

  componentWillReceiveProps(nextProps) {
    const { selectedPageId } = this.props;
    const { selectedPageId: nextSelectedPageId } = nextProps;

    if (selectedPageId !== nextSelectedPageId) {
      const { stage: nextStage, fallbackUrl: nextFallbackUrl } = nextProps;

      this.setState({
        link: createArticleLinkByStage(
          nextStage,
          nextSelectedPageId,
          nextFallbackUrl
        )
      });
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.checkAlignment);
  }

  alignTo(side) {
    this.setState({
      align: side
    });
  }

  // check if Content fits the window width and change alignment side, if it's not
  checkAlignment = () => {
    if (this.state.isOpened && this._contentElm) {
      const contentNode = findDOMNode(this._contentElm);
      const contentRect = contentNode.getBoundingClientRect();

      const leftEdgeX = contentRect.left || contentRect.x;
      const rightEdgeX = leftEdgeX + contentRect.width;

      const windowWidth =
        window.innerWidth ||
        window.document.clientWidth ||
        window.document.body.clientWidth;

      if (rightEdgeX > windowWidth) {
        this.alignTo(ALIGN_RIGHT);
      } else if (rightEdgeX + contentRect.width < windowWidth) {
        this.alignTo(ALIGN_LEFT);
      }
    }
  };

  close = () => {
    this.setState({
      isOpened: false
    });
  };

  handleTriggerMouseEnter = () => {
    this.closeTimer && clearTimeout(this.closeTimer);
    this.setState({ isOpened: true }, this.checkAlignment);
  };

  handleMouseLeave = () => {
    this.closeTimer = setTimeout(this.close, 500);
  };

  handleContentMouseEnter = () => {
    this.closeTimer && clearTimeout(this.closeTimer);
  };

  render() {
    const { isOpened, align, link } = this.state;
    const { stage, productName, showStageWarningLink } = this.props;

    return (
      <div className={stageWarningBem({ opened: isOpened, align })()}>
        <div
          className={stageWarningTriggerBem()}
          onMouseEnter={this.handleTriggerMouseEnter}
          onMouseLeave={this.handleMouseLeave}
        >
          <div className={stageWarningIconBem()} />
        </div>
        {isOpened && (
          <div
            className={stageWarningContentBem()}
            ref={elm => (this._contentElm = elm)}
            onMouseEnter={this.handleContentMouseEnter}
            onMouseLeave={this.handleMouseLeave}
          >
            {stage === 'eap' &&
              `You are viewing ${productName} documentation, which is still in development. `}
            {stage === 'outdated' &&
              `You are viewing the documentation for an earlier version of ${productName}. `}
            {showStageWarningLink && link && (
              <a href={link.url} className={stageWarningLinkBem()}>
                {link.text}
              </a>
            )}
          </div>
        )}
      </div>
    );
  }
}

export default StageWarning;
