import * as ActionTypes from 'constants/action-types';

export const stickerHeightChanged = (id, height) => ({
  type: ActionTypes.STICKER_HEIGHT_CHANGED,
  id,
  height
});

export const setStickersRelativeBlocksOffsets = offsets => ({
  type: ActionTypes.SET_STICKERS_RELATIVE_BLOCKS_OFFSETS,
  offsets
});

export const registerSideBlock = (id, relId, content) => ({
  type: ActionTypes.REGISTER_SIDEBLOCK,
  id,
  relId,
  content
});

export const unregisterSideBlock = id => ({
  type: ActionTypes.UNREGISTER_SIDEBLOCK,
  id
});

export const clearSideBlocks = () => ({
  type: ActionTypes.CLEAR_SIDEBLOCKS
});
