import * as ActionTypes from 'constants/action-types';
import { handleError } from './error';

export function treeDataReady(treeData) {
  return {
    type: ActionTypes.TREE_DATA_READY,
    treeData
  };
}

export function expandNode(nodeId) {
  return {
    type: ActionTypes.EXPAND_NODE,
    nodeId
  };
}

export function collapseNode(nodeId) {
  return {
    type: ActionTypes.COLLAPSE_NODE,
    nodeId
  };
}

export const selectNode = nodeId => ({
  type: ActionTypes.SELECT_NODE,
  nodeId
});

export const selectPrevAnchorNode = anchorNodeId => ({
  type: ActionTypes.SELECT_PREV_ANCHOR_NODE,
  anchorNodeId
});

export const selectAnchorNode = anchorNodeId => ({
  type: ActionTypes.SELECT_ANCHOR_NODE,
  anchorNodeId
});

export function loadTreeData(url) {
  return {
    type: ActionTypes.LOAD_TREE_DATA,
    url
  };
}

export function fetchTreeData(url) {
  return dispatch => {
    dispatch(loadTreeData(url));
    return fetch(url)
      .then(resp => resp.json())
      .then(data => dispatch(treeDataReady(data)))
      .catch(error => dispatch(handleError(error, 'toc')));
  };
}

export function filterByTitle(term) {
  return {
    type: ActionTypes.FILTER_BY_TITLE,
    term
  };
}
