import * as Type from 'constants/action-types';
import { handleError } from './error';
import { clearSideBlocks } from './side-blocks';

export const startFetchingArticle = () => ({
  type: Type.ARTICLE_FETCH_START
});

export const reportArticleFetchCompleted = () => ({
  type: Type.ARTICLE_FETCH_COMPLETED
});

export const articleScrollStated = () => ({
  type: Type.ARTICLE_SCROLL_TO_ANCHOR_STARTED
});

export const articleScrollEnded = () => ({
  type: Type.ARTICLE_SCROLL_TO_ANCHOR_ENDED
});

export const setArticleFallbackDisqusId = (fallbackDiscussId = null) => ({
  type: Type.ARTICLE_SET_FALLBACK_DISQUS_ID,
  fallbackDiscussId
});

export const reportArticleFetchError = error => dispatch => {
  dispatch(clearSideBlocks());
  dispatch({ type: Type.ARTICLE_FETCH_FAILED });
  dispatch(handleError(error, 'article'));
};

export const sendFeedback = feedback => (dispatch, getState) => {
  const state = getState();
  const feedbackWithMetaInfo = {
    version: state.config.get('productVersion', null), // null is default value
    articleId: state.tree.get('selectedPageId'),
    url: window.location.href,
    ...feedback
  };

  return fetch(state.config.get('feedbackUrl'), {
    method: 'POST',
    mode: 'no-cors',
    body: JSON.stringify(feedbackWithMetaInfo)
  }).catch(e => dispatch(handleError(e, 'feedback')));
};

export const setSectionSwitcherLabel = label => ({
  type: Type.SELECT_SECTIONS_LABEL,
  label
});
