import * as Type from 'constants/action-types';
import { storeToJs } from 'utils/store-to-js';

const ACTION_LOG_LIMIT = 10;
const SHOULD_STORE_ACTIONS = true;
const LOG_LOCAL_STORAGE_KEY = 'actionsLog';

const NEW_RELIC = window.newrelic || window.NREUM;
const ERROR_PROP_PREFIX = 'jb_';

/* eslint no-unused-vars: ["error", { "args": "none" }] */
export const reportErrorsMiddleware = store => next => action => {
  const result = next(action);

  let storage;
  try {
    storage = window.localStorage;
  } catch (e) {
    storage = null;
  }

  if (SHOULD_STORE_ACTIONS && storage) {
    const recordedActions = storage.getItem(LOG_LOCAL_STORAGE_KEY)
      ? JSON.parse(storage.getItem(LOG_LOCAL_STORAGE_KEY))
      : [];
    recordedActions.push(action.type); // TODO: also store data?
    // store only the limited amount of last actions
    const limitedRecordedActions = recordedActions.slice(
      Math.max(recordedActions.length - ACTION_LOG_LIMIT, 0)
    );
    localStorage.setItem(
      LOG_LOCAL_STORAGE_KEY,
      JSON.stringify(limitedRecordedActions)
    );
  }

  if (action.type === Type.HANDLE_ERROR) {
    const error = Object.assign({}, action.error, {
      message: `${action.error.message}, ${action.owner}`,
      [`${ERROR_PROP_PREFIX}owner`]: action.owner || {},
      [`${ERROR_PROP_PREFIX}actions`]:
        (storage && storage.getItem(LOG_LOCAL_STORAGE_KEY)) || [],
      [`${ERROR_PROP_PREFIX}store`]: store ? storeToJs(store.getState()) : {}
    });

    if (NEW_RELIC) {
      NEW_RELIC.noticeError(error);

      // eslint-disable-next-line no-console
      console.log(
        `Reported "${action.error}" from "${action.owner}" to NewRelic`
      );
    }
  }

  return result;
};
