import { DEFAULT_KEYMAP, MAC_OS_X_LATEST } from 'constants/keymaps';

import queryString from 'query-string';

/**
 * A generic function for searching or filtering items by ID.
 *
 * i.e.:
 *
 * ```
 * anyCollectionOfItemsWithIds.filter(byId(whichId)); => only items with this ID
 * anyCollectionOfItemsWithIds.find(byId(whichId)); => first item which has this ID
 * anyCollectionOfItemsWithIds.findLatests(byId(whichId)); => last items which has this ID
 * ```
 *
 * @param {String} id
 */
const byId = id => item => item.get('id') === id;

/**
 * Takes a list of keymaps, a fallback value and URL location and suggests
 * a keymap ID to select from the given list of keymaps at start, when we are not aware
 * of the keymaps user has. Guarantees that keymap exists in the list if `fallback` ID is
 * also in this list.
 *
 * Intended to be called only once, when we got the config.json (or when it was renewed).
 *
 * If there's a keymap defined in URL, it's a highest priority and so
 * it is returned just right away.
 *
 * Then, if user has macOS and there's a keymap with the corresponding ID in the list,
 * then this ID is returned. If there's no such keymap in the list, it checks for a previous
 * version of macOS and again, it is returned when it exists in the list.
 *
 * If all of these failed, it returnes the provided `fallback` ID.
 *
 * @param {Map} allPrimary list of primary keymaps
 * @param {String} defaultPrimary default primary keymap
 * @param {Map} allSecondary list of secondary keymaps
 * @param {String} defaultSecondary default secondary keymap

 */
export const suggestInitialKeymap = ({
  allPrimary,
  defaultPrimary,
  allSecondary,
  defaultSecondary
}) => {
  const location = window.location;
  const keymapFromUrl = queryString.parse(location.search).keymap;
  const allKeymaps = allPrimary.concat(allSecondary);

  if (keymapFromUrl && allKeymaps.find(byId(keymapFromUrl))) {
    return keymapFromUrl;
  }

  const isMac = window.navigator.platform.indexOf('Mac') !== -1;

  if (isMac) {
    if (allSecondary.find(byId(defaultSecondary))) {
      return defaultSecondary;
    }

    if (allSecondary.find(byId(MAC_OS_X_LATEST))) {
      return MAC_OS_X_LATEST;
    }
  }

  if (defaultPrimary && allPrimary.find(byId(defaultPrimary))) {
    return defaultPrimary;
  }

  if (defaultSecondary && allSecondary.find(byId(defaultSecondary))) {
    return defaultSecondary;
  }

  return DEFAULT_KEYMAP;
};
