import React, { Component } from 'react';
import { Portal } from 'react-portal';
import PropTypes from 'prop-types';

import LightBox from './lightbox.jsx';

import { isNotKeyPressed } from 'utils/link-helpers';
import {
  lightboxLinkBem,
  lightboxLinkTriggerBem,
  lightboxLinkWrapperBem
} from './_bem';
import ActionCircle from 'components/action-circle';

const Aux = props => props.children;

export default class LightboxLink extends Component {
  static propTypes = {
    imageSrc: PropTypes.string.isRequired,
    children: PropTypes.arrayOf(PropTypes.element)
  };

  constructor(props) {
    super(props);
    this.state = {
      isOpen: false
    };
  }

  onOpen = e => {
    if (isNotKeyPressed(e)) {
      e.preventDefault();
      this.setState({ isOpen: true });
    }
  };

  onClose = e => {
    e.preventDefault();
    this.setState({ isOpen: false });
  };

  render() {
    const { imageSrc, children } = this.props;
    const { isOpen } = this.state;

    return (
      <Aux>
        <span className={lightboxLinkWrapperBem()} onClick={this.onOpen}>
          <a className={lightboxLinkBem.mix('_show-img')()} href={imageSrc}>
            {children}
          </a>
          <ActionCircle className={lightboxLinkTriggerBem()} text="+" />
        </span>
        {isOpen && (
          <Portal>
            <LightBox imageSrc={imageSrc} onClose={this.onClose} />
          </Portal>
        )}
      </Aux>
    );
  }
}
