import React from 'react';
import PropTypes from 'prop-types';
import {
  actionCircleBem,
  actionCircleBackgroundFigureBem,
  actionCircleWrapperBem,
  actionCircleFrontFigureBem,
  actionCircleContentBem
} from './_bem';

const RADIUS = 44;
const SINGLE_GAP_LENGTH = 20;

const DASHES_LENGTH = 8;
const GAPS_LENGTH = 4;

const ActionCircle = ({ dashed, isLoading, text, className, ...restProps }) => {
  let dashArray = '';
  if (dashed) {
    dashArray = `${DASHES_LENGTH}, ${GAPS_LENGTH}`;
  } else if (isLoading) {
    const fullCircleLength = 2 * Math.PI * RADIUS;
    dashArray = `${fullCircleLength - SINGLE_GAP_LENGTH}, ${SINGLE_GAP_LENGTH}`;
  }
  return (
    <span className={actionCircleBem.mix(className)()} {...restProps}>
      <svg
        className={actionCircleWrapperBem({ loading: isLoading })()}
        viewBox="0 0 100 100"
        preserveAspectRatio="xMidYMid meet"
        focusable="false"
      >
        <circle
          className={actionCircleBackgroundFigureBem()}
          cx="50"
          cy="50"
          r="50"
        />
        <circle
          className={actionCircleFrontFigureBem()}
          strokeWidth="4"
          strokeDasharray={dashArray}
          fill="transparent"
          cx="50"
          cy="50"
          r={RADIUS}
        />
      </svg>
      <div className={actionCircleContentBem()}>{text}</div>
    </span>
  );
};

ActionCircle.propTypes = {
  dashed: PropTypes.bool,
  isLoading: PropTypes.bool,
  text: PropTypes.string,
  className: PropTypes.string
};

export default ActionCircle;
