import queryString from 'query-string';
import { history } from 'utils/history';

/**
 * Store given parameters in the browser history as `?...&key=value`
 *
 * @param {Object} params parameters that should be applied to query-string
 */
export const applyParamsToQueryString = params => {
  const location = window.location;
  const search = queryString.parse(location.search);
  history.push(
    Object.assign({}, location, {
      search: queryString.stringify({ ...search, ...params })
    })
  );
};

/**
 * Remove given parameter from the browser history
 *
 * @param {Object} paramName parameters that should be removed from query-string
 */
export const removeParamFromQueryString = paramName => {
  const location = window.location;
  const search = queryString.parse(location.search);
  delete search[paramName];
  history.push(
    Object.assign({}, location, {
      search: queryString.stringify(search)
    })
  );
};
