import * as Type from 'constants/action-types';

import { suggestInitialKeymap } from 'utils/suggest-initial-keymap';
import { applyParamsToQueryString } from 'utils/querystring-helpers';

export const changeKeymap = keymap => ({
  type: Type.CHANGE_KEYMAP,
  keymap
});

export const changeSection = section => {
  applyParamsToQueryString({ section });

  return {
    type: Type.CHANGE_SECTION,
    section
  };
};

export const showNavigation = () => ({ type: Type.SHOW_NAVIGATION });
export const hideNavigation = () => ({ type: Type.HIDE_NAVIGATION });

export const showSearch = () => ({ type: Type.SHOW_SEARCH });
export const hideSearch = () => ({ type: Type.HIDE_SEARCH });

export const clearConfig = () => ({ type: Type.CLEAR_CONFIG });

export const suggestKeymap = keymapSuggestionData => dispatch =>
  dispatch(changeKeymap(suggestInitialKeymap(keymapSuggestionData)));

export const setMobileMode = enable => ({
  type: Type.SET_MOBILE_MODE,
  enable
});
