import * as Type from 'constants/action-types';
import { Map, List, fromJS } from 'immutable';

const initialConfig = new Map({
  webmaster: 'webmaster@jetbrains.com',
  keymaps: null,
  sections: null,
  searchService: null
});

export function config(state = initialConfig, action) {
  switch (action.type) {
    case Type.CONFIG_FETCH_START:
      return initialConfig;

    case Type.CONFIG_FETCH_SUCCESS:
      const configMap = fromJS(action.data);
      const mergedState = state.merge(configMap);

      // keymaps are grouped like this:
      //
      // keymaps
      //   |--- primary
      //   |      |--- default: some_id
      //   |      |--- values: [ { id: some_id, name: some_name } ]
      //   |--- secondary
      //          |--- default: some_id
      //          |--- values: [ { id: some_id, name: some_name } ]
      //
      // we need to collect all the IDs from both arrays for future use in Keystroke component
      if (configMap.get('keymaps')) {
        const primaryValues =
          configMap.getIn(['keymaps', 'primary', 'values']) ||
          configMap.getIn(['keymaps', 'values']) ||
          new List();
        const secondaryValues =
          configMap.getIn(['keymaps', 'secondary', 'values']) || new List();
        // join the arrays and exclude the items with duplicate IDs
        const allKeymaps = primaryValues
          .concat(secondaryValues)
          .groupBy(x => x.get('id'))
          .map(x => x.first())
          .toList();
        return mergedState.setIn(['keymaps', 'all'], allKeymaps);
      }

      return mergedState;

    case Type.SET_AVAILABLE_SECTIONS:
      return state.withMutations(mutable =>
        mutable.set('sections', List(action.list))
      );

    case Type.CLEAR_CONFIG:
      return initialConfig;

    default:
      return state;
  }
}
