import React from 'react';
import PropTypes from 'prop-types';

import { groupItemBem } from './_bem';

const GroupItem = ({ className, children, noMargin }) => (
  <div className={groupItemBem({ 'no-margin': noMargin }).mix(className)()}>
    {children}
  </div>
);

GroupItem.propTypes = {
  className: PropTypes.string,
  children: PropTypes.any,
  noMargin: PropTypes.bool
};

export default GroupItem;
