const getCumulativeOffset = (element, container = null) => {
  const containerElement =
    container && (container.offsetTop || container.offsetTop === 0)
      ? container
      : document.querySelector(container);

  let currentElement = element;
  let top = 0;
  let left = 0;

  do {
    top += currentElement.offsetTop || 0;
    left += currentElement.offsetLeft || 0;
    currentElement = currentElement.offsetParent;
  } while (currentElement && currentElement !== containerElement);

  return {
    top: top,
    left: left
  };
};

export const getCumulativeOffsetTop = (...args) =>
  getCumulativeOffset(...args).top;

export const getCumulativeOffsetLeft = (...args) =>
  getCumulativeOffset(...args).left;

export default getCumulativeOffset;
