import block from './bem-class-name';
import { splitBevisModifiers } from './_utils';

export const bevis = name => (element, elementMods) => {
  const bemBlock = block(name);
  bemBlock.mix = (className = '') => block(name).mix(className);

  if (typeof element === 'string') {
    return bemBlock(element).mix(splitBevisModifiers(elementMods));
  }

  // if typeof `element` is object, thus it is modifiers for the block
  return bemBlock.mix(splitBevisModifiers(element));
};
