import { hasClass } from 'utils/parsed-html-helpers';

export function extractTabsData(parsedDomNode) {
  const tabsContentElements =
    parsedDomNode.children &&
    parsedDomNode.children.filter(node => hasClass(node, 'tabs__content'));

  const tabsTitles = [];
  const tabsContent = [];

  for (const tabsContentElement of tabsContentElements) {
    tabsTitles.push(tabsContentElement.attribs['data-title']);
    tabsContent.push(tabsContentElement);
  }

  return { tabsTitles, tabsContent };
}
