import React, { Component } from 'react';
import debounce from 'utils/debounce';
import PropTypes from 'prop-types';
import {
  debouncedFilterInputBem,
  debouncedFilterInputWrapperBem,
  debouncedFilterInputClearBem
} from './_bem';

const DEBOUNCE_TIMEOUT = 500;

class DebouncedFilterInput extends Component {
  static propTypes = {
    term: PropTypes.string.isRequired,
    filter: PropTypes.func.isRequired
  };

  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.debouncedFilter = debounce(props.filter, DEBOUNCE_TIMEOUT);
    this.state = { term: props.term || '' };
  }

  componentWillReceiveProps(newProps) {
    this.setState(() => {
      return { term: newProps.term };
    });
  }

  handleChange(evt) {
    const filterValue = evt.target.value;
    this.setState(() => {
      return { term: filterValue };
    });
    this.debouncedFilter(filterValue);
  }

  clearFilter = () => {
    this.props.filter('');
    this.input.focus();
  };

  handleRef = input => {
    this.input = input;
  };

  render() {
    const { term } = this.state;

    return (
      <label
        className={debouncedFilterInputWrapperBem({ searching: !!term })()}
      >
        <input
          className={debouncedFilterInputBem({ searching: !!term })()}
          placeholder="Filter"
          tabIndex={9}
          value={term}
          onChange={this.handleChange}
          ref={this.handleRef}
        />
        {term && (
          <div
            className={debouncedFilterInputClearBem()}
            onClick={this.clearFilter}
          />
        )}
      </label>
    );
  }
}

export default DebouncedFilterInput;
