import { connect } from 'react-redux';
import Link from 'components/preserve-qs-link';

import Toc from 'components/toc';

const mapStateToProps = ({ tree, app }) => ({
  topLevelIds: tree.get('topLevelIds').toJS(),
  linkComponent: Link,
  data: {
    ...tree.getIn(['entities', 'pages']).toJS(),
    ...tree.getIn(['entities', 'anchors']).toJS()
  },
  ...(tree.get('filterData')
    ? { filteredIds: tree.get('filterData').toJS().showingIds }
    : {}),
  isLoading: !tree.get('treeDataReady'),
  selectedId: tree.get('selectedAnchorId') || tree.get('selectedPageId'),
  selectedSection: app.get('currentSection')
});

export default connect(mapStateToProps)(Toc);
